import { ActionIcon, Menu } from "@mantine/core";
import { IconDotsVertical } from "@tabler/icons-react";
import { ReactElement } from "react";
function getGithubURL(
  owner: string,
  name: string,
  commitOrBranch: string,
  filename: string,
  path: "blob" | "blame" = "blob"
) {
  return `https://github.com/${owner}/${name}/${path}/${commitOrBranch}/${filename}`;
}
export default function ActionMenu({
  owner,
  name,
  defaultBranchName,
  commitOid,
  parentCommitOid,
  filename,
}: Readonly<{
  owner: string;
  name: string;
  defaultBranchName?: string;
  commitOid: string;
  parentCommitOid?: string;
  filename: string;
}>): ReactElement {
  return (
    <Menu shadow="md" width={200}>
      <Menu.Target>
        <ActionIcon variant="transparent">
          <IconDotsVertical stroke={1} />
        </ActionIcon>
      </Menu.Target>
      <Menu.Dropdown>
        {defaultBranchName != null && (
          <Menu.Item>
            <a
              href={getGithubURL(owner, name, defaultBranchName, filename)}
              target="_blank"
              rel="noreferrer"
            >
              View in {defaultBranchName}
            </a>
          </Menu.Item>
        )}
        {parentCommitOid != null && (
          <Menu.Item>
            <a
              href={getGithubURL(owner, name, parentCommitOid, filename)}
              target="_blank"
              rel="noreferrer"
            >
              View in parent commit
            </a>
          </Menu.Item>
        )}
        <Menu.Item>
          <a
            href={getGithubURL(owner, name, commitOid, filename)}
            target="_blank"
            rel="noreferrer"
          >
            View in this commit
          </a>
        </Menu.Item>
        <Menu.Divider />
        {defaultBranchName != null && (
          <Menu.Item>
            <a
              href={getGithubURL(
                owner,
                name,
                defaultBranchName,
                filename,
                "blame"
              )}
              target="_blank"
              rel="noreferrer"
            >
              Blame
            </a>
          </Menu.Item>
        )}
      </Menu.Dropdown>
    </Menu>
  );
}
