import {
  ActionIcon,
  Button,
  Checkbox,
  Group,
  Loader,
  Popover,
  SegmentedControl,
  SegmentedControlItem,
  Stack,
  Text,
} from "@mantine/core";
import { IconGitMerge, IconLayoutDashboard } from "@tabler/icons-react";
import { useAtom, useAtomValue } from "jotai";
import { ReactElement, Suspense } from "react";
import { useFragment } from "react-relay";
import { graphql } from "relay-runtime";
import {
  reviewSplitByCommitSelector,
  sidepanelLayoutAtom,
  SidepanelLayoutType,
  useSetSettings,
} from "../../state/settings";
import { Styles } from "../../styles";
import { ElementSpacer } from "../dashboard/PullRequest";
import SubmitReviewPanel from "./SubmitReviewPanel";
import VersionSelector from "./VersionSelector";
import { FooterPullRequestFragment$key } from "./__generated__/FooterPullRequestFragment.graphql";
import { css } from "@emotion/react";

const fragment = graphql`
  fragment FooterPullRequestFragment on PullRequest {
    commits(first: 30) {
      totalCount
      edges {
        __typename
      }
    }
    mergeable
    viewerDidAuthor
    viewerCanMergeAsAdmin
    ...VersionSelectorFragment
    ...SubmitReviewPanelFragment
  }
`;

type Props = Readonly<{
  pr: FooterPullRequestFragment$key;
}>;

const SIDEPANEL_LAYOUT_OPTIONS: SegmentedControlItem[] = [
  { value: "none", label: "None" },
  { value: "timeline", label: "Timeline" },
  { value: "comments", label: "Comments" },
];

export default function Footer(props: Props): ReactElement {
  return (
    <Suspense fallback={<Loader />}>
      <FooterImpl {...props} />
    </Suspense>
  );
}

function FooterImpl({ pr: prRef }: Props): ReactElement {
  const setSettings = useSetSettings();
  const reviewSplitByCommit = useAtomValue(reviewSplitByCommitSelector);
  // @ts-expect-error 2769
  const pr = useFragment(fragment, prRef);
  const [sidepanelLayout, setSidepanelLayout] = useAtom(sidepanelLayoutAtom);
  return (
    <Group gap="sm" justify="flex-end" wrap="nowrap">
      {pr.mergeable === "MERGEABLE" &&
        (pr.viewerDidAuthor || pr.viewerCanMergeAsAdmin) && (
          <Button
            size="compact-sm"
            right="Merge"
            variant="light"
            color="violet"
          >
            <IconGitMerge />
          </Button>
        )}
      <VersionSelector fragment={pr} />
      <SubmitReviewPanel pr={pr} />
    </Group>
  );
}
