import { useMemo } from "react";
import { useFragment } from "react-relay";
import { graphql } from "relay-runtime";
import { useGetCompareMultipleCommits } from "../../api";
import { filterNulls, nonNull, only } from "../../utils";
import { useVersionHistoryFragment$key } from "./__generated__/useVersionHistoryFragment.graphql";
import dayjs, { Dayjs } from "dayjs";

const fragment = graphql`
  fragment useVersionHistoryFragment on PullRequest {
    baseRepository {
      owner {
        login
      }
      name
    }
    baseRef {
      name
    }
    createdAt
    lastCommit: commits(last: 1) {
      edges {
        node {
          commit {
            oid
          }
        }
      }
    }
    timelineItems(
      first: 30
      itemTypes: [
        HEAD_REF_FORCE_PUSHED_EVENT
        BASE_REF_FORCE_PUSHED_EVENT
        BASE_REF_CHANGED_EVENT
        PULL_REQUEST_COMMIT
      ]
    ) {
      edges {
        node {
          __typename
          ... on PullRequestCommit {
            commit {
              oid
              authoredDate
            }
          }
          ... on BaseRefChangedEvent {
            __typename
            createdAt
            previousRefName
          }
          ... on BaseRefForcePushedEvent {
            __typename
            createdAt
            ref {
              name
            }
          }
          ... on HeadRefForcePushedEvent {
            __typename
            createdAt
            ref {
              name
            }
            afterCommit {
              oid
            }
            beforeCommit {
              oid
              committedDate
            }
          }
        }
      }
    }
    commits(first: 30) {
      edges {
        node {
          commit {
            oid
          }
        }
      }
    }
    viewerLatestReview {
      commit {
        id
        oid
      }
    }
  }
`;

export type Version = {
  baseRef: string;
  headOid: string;
  commitOids: string[];
  number: number;
  nodeIds: string[];
  createdAt?: Dayjs;
  abbreviatedOid: string;
  author: string;
};

export type ViewerLatestReview = {
  oid: string;
  number: number | null;
  abbreviatedOid: string;
  nodeId: string;
} | null;

export type VersionHistory = {
  versions: Version[];
  viewerLastReview: ViewerLatestReview;
};

export default function useVersionHistory(
  prRef: useVersionHistoryFragment$key
): VersionHistory {
  // @ts-expect-error 2769
  const pr = useFragment(fragment, prRef);
  const timelineItems = filterNulls(
    pr.timelineItems.edges?.map((e) => e?.node)
  );
  // A version is the latest commit __before__ a force push
  // These need to be recalcuated when the PR changes as this component is not necessarily unmounted
  /* eslint-disable react-hooks/exhaustive-deps */
  const versionToHeadOid = useMemo(() => new Map<number, string>(), [pr]);
  const baseRefs = useMemo(() => new Map<number, string>(), [pr]);
  const idxToBaseRef = useMemo(() => new Map<number, string>(), [pr]);
  const versionTimes = useMemo(() => new Map<number, string>(), [pr]);
  const versionOidsToNumber = useMemo(() => new Map<string, number>(), [pr]);
  /* eslint-enable react-hooks/exhaustive-deps */
  useMemo(() => {
    let latestVersion = 1;
    let previousCommit = null;
    let ref = pr.baseRef?.name;
    if (pr.baseRef == null || ref == null) {
      return null;
    }
    for (const [idx, timelineItem] of Array.from(
      timelineItems.entries()
    ).reverse()) {
      idxToBaseRef.set(timelineItems.length - 1 - idx, ref);
      if (timelineItem.__typename === "BaseRefChangedEvent") {
        ref = timelineItem.previousRefName;
      } else if (timelineItem.__typename === "BaseRefForcePushedEvent") {
        if (timelineItem.ref?.name != null) {
          ref = timelineItem.ref?.name;
        }
      }
    }
    for (const [idx, timelineItem] of timelineItems.entries()) {
      ref = idxToBaseRef.get(idx);
      if (ref == null) {
        throw new Error("unable to find baseRef for verison");
      }
      if (timelineItem.__typename === "HeadRefForcePushedEvent") {
        if (!versionOidsToNumber.has(timelineItem.beforeCommit?.oid)) {
          versionToHeadOid.set(latestVersion, timelineItem.beforeCommit?.oid);
          baseRefs.set(latestVersion, ref);
          versionTimes.set(
            latestVersion,
            timelineItem.beforeCommit?.committedDate
          );
          versionOidsToNumber.set(timelineItem.afterCommit?.oid, latestVersion);
          latestVersion++;
        }
        versionToHeadOid.set(latestVersion, timelineItem.afterCommit?.oid);
        baseRefs.set(latestVersion, ref);
        versionTimes.set(latestVersion, timelineItem.createdAt);
        versionOidsToNumber.set(timelineItem.afterCommit?.oid, latestVersion);
        latestVersion++;
      } else if (timelineItem.__typename === "PullRequestCommit") {
        previousCommit = timelineItem.commit;
      }
    }
    if (versionToHeadOid.size > 0 && previousCommit != null) {
      // aka the there has been a commit since the last force push
      versionToHeadOid.set(latestVersion - 1, previousCommit.oid);
      // TODO unclear if this is the right date because sometimes it's earlier than the force push
      // if (
      //   moment(versionTimes.get(latestVersion - 1)).diff(
      //     previousCommit.authoredDate
      //   ) < 0
      // ) {
      //   versionTimes.set(latestVersion - 1, previousCommit.authoredDate);
      // }
    }
    if (versionToHeadOid.size === 0) {
      versionToHeadOid.set(
        1,
        only(pr.lastCommit.edges?.map((e) => e?.node))?.commit.oid
      );
      baseRefs.set(1, nonNull(pr.baseRef?.name));
    }
  }, [
    baseRefs,
    idxToBaseRef,
    pr,
    timelineItems,
    versionOidsToNumber,
    versionTimes,
    versionToHeadOid,
  ]);
  const items = useMemo(
    () =>
      Array.from(versionToHeadOid.entries())
        .map(([idx, sha]) => ({
          baseRef: baseRefs.get(idx),
          headOid: sha,
        }))
        .filter((item) => item.baseRef != null),
    [baseRefs, versionToHeadOid]
  );
  const history = useGetCompareMultipleCommits(
    nonNull(pr.baseRepository?.owner?.login),
    nonNull(pr.baseRepository?.name),
    items
  );
  const viewerLatestReviewOid = pr.viewerLatestReview?.commit?.oid;
  let viewerLastReview = null;
  if (viewerLatestReviewOid != null) {
    viewerLastReview = {
      oid: viewerLatestReviewOid,
      abbreviatedOid: viewerLatestReviewOid.slice(0, 7),
      number: versionOidsToNumber.get(viewerLatestReviewOid) ?? null,
      nodeId: nonNull(pr.viewerLatestReview?.commit?.id),
    };
  }
  return {
    viewerLastReview: viewerLastReview,
    versions: filterNulls(
      // incredibly inefficient way to get around a null check
      history
        ?.map(({ data }) => data)
        ?.filter((data) => data != null)
        ?.map((h, idx) => ({
          commitOids: h.commits.map((c) => c.sha),
          number: idx + 1,
          headOid: h.commits[h.commits.length - 1].sha,
          abbreviatedOid: h.commits[h.commits.length - 1].sha.slice(0, 7),
          baseRef: baseRefs.get(idx + 1)!,
          nodeIds: h.commits.map((c) => c.node_id),
          author: h.commits[h.commits.length - 1].author.login,
          createdAt:
            versionTimes.get(idx + 1) == null
              ? undefined
              : dayjs(versionTimes.get(idx + 1)),
        }))
    ),
  };
}
