import { useAtom, useAtomValue } from "jotai";
import { useCallback, useMemo } from "react";
import { useFragment, useMutation } from "react-relay";
import { RecordSourceSelectorProxy, graphql } from "relay-runtime";
import { filterNulls } from "../../utils";
import {
  pendingThreadCommentsAtom as pendingCommentsThreadIdToNodeIdAtom,
  pendingThreadsAtom,
} from "../review/settings";
import {
  reviewThreadCommentMutation,
  reviewThreadCommentMutation$data,
} from "./__generated__/reviewThreadCommentMutation.graphql";
import { reviewThreadCommentMutationPullRequestFragment$key } from "./__generated__/reviewThreadCommentMutationPullRequestFragment.graphql";
import StoreUtils from "./storeUtils";

const query = graphql`
  fragment reviewThreadCommentMutationPullRequestFragment on PullRequest {
    reviewThreads(first: 20) {
      edges {
        node {
          id
          comments(first: 20) {
            edges {
              node {
                id
                body
                ... on PullRequestReviewComment {
                  isPending
                }
              }
            }
          }
        }
      }
    }
  }
`;

const addThreadCommentMutation = graphql`
  mutation reviewThreadCommentMutation(
    $input: AddPullRequestReviewThreadReplyInput!
  ) {
    addPullRequestReviewThreadReply(input: $input) {
      comment  {
        body
        pullRequestReview {
          id
          state
        }
      }
    }
  }
`;

export default function useReviewThreadCommentMutation({
  pr: prRef,
}: {
  pr: reviewThreadCommentMutationPullRequestFragment$key;
}): [
  (
    pullRequestReviewId: string,
    onComplete: (pendingReviewId: string) => void
  ) => void,
  boolean
] {
  const [threadComments, setThreadComments] = useAtom(
    pendingCommentsThreadIdToNodeIdAtom
  );
  const pendingThreads = useAtomValue(pendingThreadsAtom);
  const pr = useFragment(query, prRef);
  const threadCommentIdToBody = useMemo(() => {
    const threads = filterNulls(pr.reviewThreads?.edges?.map((e) => e?.node));
    const comments = filterNulls(
      threads.map((t) => t.comments?.edges?.map((e) => e?.node)).flat()
    ).filter((c) => c.isPending === true);
    return new Map(comments.map((c) => [c.id, c.body]));
  }, [pr]);
  const [mutateReviewThread, isMutatingReviewThread] =
    useMutation<reviewThreadCommentMutation>(addThreadCommentMutation);
  const pendingThreadIds = useMemo(() => new Set<string>(), []);
  const onThreadComplete = useCallback(
    (
      threadId: string,
      pendingReviewId: string,
      onComplete: (pendingReviewId: string) => void
    ) => {
      setThreadComments((tc) => tc.delete(threadId));
      pendingThreadIds.delete(threadId);
      if (pendingThreadIds.size === 0) {
        onComplete(pendingReviewId);
      }
    },
    [setThreadComments, pendingThreadIds]
  );
  // const [pendingReviewIds, setPendingReviewId] = useAtom(
  //   pendingReviewThreadsAtom
  // );
  const updater = useCallback(
    (threadId: string) =>
      (
        store: RecordSourceSelectorProxy<reviewThreadCommentMutation$data>,
        _createdReview: reviewThreadCommentMutation$data
      ) => {
        const pendingReviewInfo = threadComments.get(threadId);
        if (pendingReviewInfo == null) {
          // console.log("no pending review info in store");
          return;
        }
        const storeUtils = new StoreUtils(store);
        storeUtils
          .get(pendingReviewInfo)
          ?.setValues({ isPending: false });
        setThreadComments((s) => s.delete(threadId));
      },
    [threadComments, setThreadComments]
  );

  return [
    (pullRequestReviewId, onComplete) => {
      const commentsToMutate = threadComments.filter(
        (threadId, nodeId) => !pendingThreads.has(nodeId)
      );
      for (const [threadId, commentId] of commentsToMutate) {
        const body = threadCommentIdToBody.get(commentId);
        pendingThreadIds.add(threadId);
        mutateReviewThread({
          variables: {
            input: {
              body: body,
              pullRequestReviewThreadId: threadId,
              pullRequestReviewId: pullRequestReviewId,
            },
          },
          updater: updater(threadId),
          onCompleted: (response) =>
            onThreadComplete(
              threadId,
              response.addPullRequestReviewThreadReply.comment.pullRequestReview
                .id,
              onComplete
            ),
        });
      }
    },
    isMutatingReviewThread,
  ];
}
