import { useAtom } from "jotai";
import { useRelayEnvironment } from "react-relay";
import { commitLocalUpdate } from "relay-runtime";
import { pendingReviewIdAtom } from "../review/settings";
import StoreUtils from "./storeUtils";
import dayjs from "dayjs";

export default function usePendingCommentUpdater(): (
  comment: string,
  pullRequestId: string
) => void {
  const env = useRelayEnvironment();
  const [pendingReviewId, setPendingReviewId] =
    useAtom(pendingReviewIdAtom);
  return (comment, pullRequestId) => {
    commitLocalUpdate(env, (store) => {
      const storeUtils = new StoreUtils(store);
      if (pendingReviewId != null) {
        storeUtils.get(pendingReviewId)?.setValues({
          isPending: true,
          body: comment,
          createdAt: dayjs().toISOString(),
        });
        return;
      }
      storeUtils
        .get(pullRequestId)
        ?.createAndAppendNodeToEdge(
          store,
          "reviews",
          { first: 30 },
          "PullRequestReviewConnection",
          "PullRequestReviewEdge",
          "PullRequestReview",
          {
            isPending: true,
            body: comment,
            createdAt: dayjs().toISOString(),
          }
        )
        ?.then(({ nodeId }) => {
          setPendingReviewId(nodeId);
        });
      // Comments
    });
  };
}
