import { useMutation } from "react-relay";
import { Disposable, graphql } from "relay-runtime";
import { unresolveMutationResolveReviewThreadMutation } from "./__generated__/unresolveMutationResolveReviewThreadMutation.graphql";


const unresolveMutation = graphql`
  mutation unresolveMutationResolveReviewThreadMutation(
    $input: UnresolveReviewThreadInput!
  ) {
    unresolveReviewThread(input: $input) {
      thread {
        isResolved
      }
    }
  }
`;

export default function useUnresolveMutation(): [
  (threadId: string, onComplete?: () => void) => Disposable,
  boolean
] {
  const [mutate, isMutating] =
    useMutation<unresolveMutationResolveReviewThreadMutation>(unresolveMutation);
  return [
    (threadId, onComplete) =>
      mutate({
        variables: {
          input: {
            threadId,
          },
        },
        onCompleted: onComplete
      }),
    isMutating,
  ];
}
