import { Button, Text } from "@mantine/core";
import { useAtom, useAtomValue } from "jotai";
import { ReactElement, useCallback } from "react";
import { useFragment } from "react-relay";
import { graphql } from "relay-runtime";
import { coalesceString, filterNulls, only } from "../../utils";
import { PullRequestReviewEvent } from "../mutations/__generated__/submitReviewMutationSubmitPullRequestReviewMutation.graphql";
import useAddReviewMutation from "../mutations/addReviewMutation";
import { useDeletePendingThreadComment } from "../mutations/pendingReviewThreadComment";
import useReviewThreadCommentMutation from "../mutations/reviewThreadCommentMutation";
import useSubmitReviewMutation from "../mutations/submitReviewMutation";
import { SubmitReviewPanelFragment$key } from "./__generated__/SubmitReviewPanelFragment.graphql";
import {
  hasPendingReviewBodyCommentAtom,
  pendingReviewBodyEditorAtom,
  pendingThreadCommentsAtom,
  pendingThreadsAtom,
} from "./settings";
import { IconCheck, IconTrash, IconX } from "@tabler/icons-react";
import { userLoginAtom } from "../../state/user";

const fragment = graphql`
  fragment SubmitReviewPanelFragment on PullRequest {
    id
    viewerCanUpdate
    author {
      login
    }
    reviews(first: 30) {
      edges {
        node {
          id
          state
          body
        }
      }
    }
    ...reviewThreadCommentMutationPullRequestFragment
    ...addReviewMutationPullRequestFragment
  }
`;

type Props = Readonly<{
  pr: SubmitReviewPanelFragment$key;
}>;

export default function SubmitReviewPanel({ pr: prRef }: Props): ReactElement {
  // @ts-expect-error 2769
  const pr = useFragment(fragment, prRef);
  const [user] = useAtom(userLoginAtom);
  const editorRef = useAtomValue(pendingReviewBodyEditorAtom);
  const hasDraftComment = useAtomValue(hasPendingReviewBodyCommentAtom);
  const threadComments = useAtomValue(pendingThreadCommentsAtom);
  const pendingReviewThreads = useAtomValue(pendingThreadsAtom);
  const [mutateThread, isMutatingThread] = useReviewThreadCommentMutation({
    pr,
  });
  const [addMutate, isAddingPR] = useAddReviewMutation(pr);
  const [submitMutate, isSubmittingPR] = useSubmitReviewMutation();
  const pendingReview = only(
    filterNulls(pr.reviews?.edges?.map((e) => e?.node)).filter(
      (n) => n.state === "PENDING"
    )
  );
  const deleteThread = useDeletePendingThreadComment();
  const isMutating = isAddingPR || isSubmittingPR || isMutatingThread;
  const [threadCommentReplies, pendingThreads] = threadComments.partition(
    (_value, key) => pendingReviewThreads.has(key)
  );
  const numComments = (hasDraftComment ? 1 : 0) + threadComments.size;
  const onThreadsComplete = useCallback(
    (state: PullRequestReviewEvent) => {
      return (pullRequestReviewId: string) => {
        const markdownDraftComment = editorRef?.current?.getMarkdown();
        submitMutate(
          pr.id,
          pullRequestReviewId,
          state,
          coalesceString(pendingReview?.body, markdownDraftComment)
        );
      };
    },
    [submitMutate, pendingReview?.body, pr.id, editorRef]
  );
  const leaveReview = useCallback(
    (state: PullRequestReviewEvent) => {
      // TODO deal with state == null review
      // TODO deal with threadCommentReplies but no review
      if (!pr.viewerCanUpdate) {
        return;
      }
      if (pendingReview != null) {
        onThreadsComplete(state)(pendingReview.id);
        return;
      }
      if (threadCommentReplies.size > 0) {
        addMutate(
          pr.id,
          pendingReview?.id ?? null,
          pendingReview?.body ?? null,
          (pendingReviewId: string) => {
            mutateThread(pendingReviewId, onThreadsComplete(state));
          }
        );
      } else {
        addMutate(
          pr.id,
          pendingReview?.id ?? null,
          pendingReview?.body ?? null,
          (pendingReviewId: string) => {
            onThreadsComplete(state)(pendingReviewId);
          }
        );
      }
    },
    [
      pr,
      pendingReview,
      threadCommentReplies.size,
      addMutate,
      mutateThread,
      onThreadsComplete,
    ]
  );
  return (
    <>
      <Button
        disabled={numComments < 1 || isMutating}
        onClick={() => {
          for (const [reviewThreadId] of threadCommentReplies) {
            deleteThread(reviewThreadId);
          }
          editorRef?.current?.setMarkdown("");
        }}
        size="compact-sm"
        variant="light"
      >
        <Text inherit mr="xs">
          Abandon
        </Text>
        <IconTrash size={16} stroke={2} />
      </Button>
      <Button
        disabled={numComments < 1 || isMutating}
        onClick={() => {
          leaveReview("COMMENT");
        }}
        size="compact-sm"
        variant="light"
        color={numComments > 0 ? "yellow" : undefined}
      >
        {numComments > 0 ? `Comment (${numComments})` : "Comment"}
      </Button>
      <Button
        disabled={pr.author?.login === user}
        loading={isMutating}
        onClick={() => {
          leaveReview("REQUEST_CHANGES");
        }}
        size="compact-sm"
        color="red"
        variant="light"
      >
        <Text mr="xs" inherit>
          Reject
        </Text>
        <IconX size={16} stroke={2} />
      </Button>
      <Button
        disabled={pr.author?.login === user}
        loading={isMutating}
        onClick={() => {
          leaveReview("APPROVE");
        }}
        size="compact-sm"
        variant="light"
        color="green"
      >
        <Text mr="xs" inherit>
          Approve
        </Text>
        <IconCheck size={16} stroke={2} />
      </Button>
    </>
  );
}
