import { useAtomValue, useSetAtom } from "jotai";
import { useCallback } from "react";
import { graphql, useFragment, useMutation } from "react-relay";
import { RecordSourceSelectorProxy } from "relay-runtime";
import { filterNulls, first } from "../../utils";
import {
  pendingReviewBodyAtom,
  pendingReviewBodyEditorAtom,
  pendingReviewIdAtom,
} from "../review/settings";
import {
  addReviewMutationAddPullRequestReviewMutation,
  addReviewMutationAddPullRequestReviewMutation$data,
} from "./__generated__/addReviewMutationAddPullRequestReviewMutation.graphql";
import { addReviewMutationPullRequestFragment$key } from "./__generated__/addReviewMutationPullRequestFragment.graphql";
import StoreUtils from "./storeUtils";

const fragment = graphql`
  fragment addReviewMutationPullRequestFragment on PullRequest {
    id
    reviewThreads(first: 20) {
      edges {
        node {
          isPending
          line
          startLine
          path
          comments(first: 20) {
            edges {
              node {
                body
              }
            }
          }
        }
      }
    }
  }
`;

const addReviewMutation = graphql`
  mutation addReviewMutationAddPullRequestReviewMutation(
    $input: AddPullRequestReviewInput!
  ) {
    addPullRequestReview(input: $input) {
      pullRequestReview {
        id
        createdAt
        ...ActivityPanelReviewFragment
        pullRequest {
          id
        }
      }
    }
  }
`;

export default function useAddReviewMutation(
  prRef: addReviewMutationPullRequestFragment$key
): [
  (
    pullRequestId: string,
    pendingPullRequestReviewId: string | null,
    body: string | null,
    onComplete: (pendingReviewId: string) => void
  ) => void,
  boolean
] {
  const pr = useFragment(fragment, prRef);
  const setPendingReviewId = useSetAtom(pendingReviewIdAtom);
  const pendingReviewBodyEditor = useAtomValue(pendingReviewBodyEditorAtom);
  const onCompleteInternal = useCallback(() => {
    setPendingReviewId(null);
    pendingReviewBodyEditor?.current?.setMarkdown("");
  }, [setPendingReviewId, pendingReviewBodyEditor]);
  const threads = filterNulls(
    pr.reviewThreads.edges
      ?.map((e) => e?.node)
      ?.filter((n) => n?.isPending === true)
  ).map((n) => {
    const comment = first(n.comments?.edges);
    return {
      body: comment?.node?.body ?? "",
      line: n.line!,
      path: n.path,
      startLine: n.startLine,
    };
  });

  const updater = useCallback(
    (pendingPullRequestReviewId: string | null) =>
      (
        store: RecordSourceSelectorProxy<addReviewMutationAddPullRequestReviewMutation$data>,
        createdReview: addReviewMutationAddPullRequestReviewMutation$data
      ) => {
        if (pendingPullRequestReviewId != null) {
          store.delete(pendingPullRequestReviewId);
        }
        const storeUtils = new StoreUtils(store);
        storeUtils
          .get(
            createdReview.addPullRequestReview.pullRequestReview.pullRequest.id
          )
          ?.appendToEdge(
            store,
            "reviews",
            { first: 30 },
            "PullRequestReviewEdge",
            createdReview.addPullRequestReview.pullRequestReview.id
          );
        // setPendingReviewId(
        //   createdReview.addPullRequestReview.pullRequestReview.id
        // );
      },
    []
  );
  const [addPR, isAddingPR] =
    useMutation<addReviewMutationAddPullRequestReviewMutation>(
      addReviewMutation
    );
  return [
    (pullRequestId, pendingPullRequestReviewId, body, onComplete) => {
      addPR({
        variables: {
          input: {
            pullRequestId: pullRequestId,
            body: body ?? undefined,
            threads,
          },
        },
        updater: updater(pendingPullRequestReviewId),
        onCompleted: (response) => {
          onCompleteInternal();
          onComplete(response.addPullRequestReview.pullRequestReview.id);
        },
      });
    },
    isAddingPR,
  ];
}
