import { Button, Card, Group, Stack, Text } from "@mantine/core";
import { IconCheckbox } from "@tabler/icons-react";
import { ReactElement, useCallback, useRef, useState } from "react";
import { graphql, useFragment } from "react-relay";
import { Styles } from "../../styles";
import { filterNulls, first, only } from "../../utils";
import ErrorBoundary from "../ErrorBoundary";
import { useToggle } from "../layout/Toggle";
import { useDeleteReviewThreadUpdater } from "../mutations/addReviewThreadUpdater";
import usePendingReviewThreadCommentUpdater, {
  useDeletePendingThreadComment,
} from "../mutations/pendingReviewThreadComment";
import useResolveMutation from "../mutations/resolveMutation";
import useUnresolveMutation from "../mutations/unresolveMutation";
import { CommentThreadPullRequestFragment$key } from "./__generated__/CommentThreadPullRequestFragment.graphql";
import Comment from "./Comment";
import { MarkdownTextareaRef } from "../editor/MarkdownTextarea";
import { MarkdownTextarea } from "../editor/LazyMarkdownTextarea";
import { CommentThreadPullRequestReviewFragment$key } from "./__generated__/CommentThreadPullRequestReviewFragment.graphql";

const reviewThreadFragment = graphql`
  fragment CommentThreadPullRequestReviewFragment on PullRequestReviewThread {
    id
    isPending
    isResolved
    line
    startLine
    comments(first: 20) {
      edges {
        node {
          id
          startLine
          line
          ...CommentFragment
        }
      }
    }
    viewerCanReply
    viewerCanResolve
    viewerCanUnresolve
  }
`;

const prFragment = graphql`
  fragment CommentThreadPullRequestFragment on PullRequest {
    reviews(first: 30) {
      edges {
        node {
          id
          state
        }
      }
    }
  }
`;

type Props = Readonly<{
  reviewThread: CommentThreadPullRequestReviewFragment$key;
  pr: CommentThreadPullRequestFragment$key;
}>;

function CommentThreadImpl({
  reviewThread: reviewThreadRef,
  pr: prRef,
}: Props): ReactElement | null {
  // return <Text>Comment thread</Text>;
  const commentThread = useFragment(reviewThreadFragment, reviewThreadRef);
  const pr = useFragment(prFragment, prRef);
  const pendingReview = only(
    filterNulls(pr.reviews?.edges?.map((e) => e?.node)).map(
      (n) => n.state == null || n.state === 'PENDING'
    )
  );
  console.log(pendingReview);
  const textareaRef = useRef<MarkdownTextareaRef>(null);
  const [isSuggesting, setIsSuggesting] = useState(false);
  const [isHidden, toggle, setIsHidden] = useToggle(
    commentThread.isResolved,
    commentThread.isResolved ? 12 : 16
  );
  const pendingThreadCommentUpdater = usePendingReviewThreadCommentUpdater();
  const deleteReply = useDeletePendingThreadComment();
  const deleteThread = useDeleteReviewThreadUpdater();
  const saveComment = useCallback(() => {
    const comment = textareaRef?.current?.getMarkdown();
    if (comment == null){
      return;
    }
    pendingThreadCommentUpdater(comment, commentThread.id);
    textareaRef?.current?.setMarkdown("");
  }, [commentThread.id, pendingThreadCommentUpdater]);
  const [resolveThread, isResolving] = useResolveMutation();
  const [unresolveThread, isUnresolving] = useUnresolveMutation();
  const [isEmpty, setIsEmpty] = useState(true);
  const comments = filterNulls(
    commentThread.comments?.edges?.map((e) => e?.node)
  );
  const resolveComments = !commentThread.isPending && (
    <Button
      size={commentThread.isResolved ? "compact-xs" : "compact-sm"}
      variant="default"
      disabled={
        !(commentThread.viewerCanResolve || commentThread.viewerCanUnresolve)
      }
      loading={isResolving || isUnresolving}
      onClick={() => {
        if (commentThread.isResolved) {
          unresolveThread(commentThread.id);
          setIsHidden(false);
        } else {
          resolveThread(commentThread.id);
          setIsHidden(true);
        }
      }}
    >
      <Group gap="xxxs">
        <IconCheckbox stroke={1} size={16} />
        {commentThread.isResolved ? "Resolved" : "Resolve"}
      </Group>
    </Button>
  );

  const showReply = commentThread.viewerCanReply && !isHidden;
  const firstComment = first(comments);
  let headlineText = null;
  if (firstComment != null) {
    if (firstComment.startLine != null) {
      headlineText = `${comments.length} comment(s) on lines ${
        firstComment.startLine
      }-${first(comments)?.line}`;
    } else {
      headlineText = `${comments.length} comment(s) on line ${firstComment.line}`;
    }
  }
  if (commentThread.isPending) {
    if (commentThread.startLine != null) {
      headlineText = `are commenting on lines ${commentThread.startLine}-${commentThread.line}`;
    } else {
      headlineText = `are commenting on line ${commentThread.line}`;
    }
  }
  return (
    <Card
      withBorder
      radius={0}
      className="comment-thread"
      color={commentThread.isPending ? "yellow" : undefined}
    >
      <Card.Section
        withBorder
        color={commentThread.isPending ? "yellow" : undefined}
      >
        <Group
          className={Styles.secondaryText}
          mx="sm"
          my={commentThread.isResolved ? 2 : 6}
        >
          <Text size={commentThread.isResolved ? "xs" : "sm"}>
            {headlineText}
          </Text>
          <Group align="center" className={Styles.floatRight}>
            {resolveComments}
            {!commentThread.isPending && toggle}
          </Group>
        </Group>
      </Card.Section>
      <Card.Section className="thread-comment middle">
        {!isHidden &&
          comments.map((n, idx) => (
            <Comment
              comment={n}
              header="none"
              bottomBorder={idx !== comments.length - 1}
              border
              key={n.id}
            />
          ))}
        {showReply && (
          <Stack gap={0}>
            <MarkdownTextarea
              converterRef={textareaRef}
              onChange={setIsEmpty}
              instanceId={commentThread.id}
              m={0}
            />
            <Group justify="space-between" m="xs" mt="xs">
              <Button
                disabled={!commentThread.isPending && isEmpty}
                onClick={() => {
                  deleteReply(commentThread.id);
                  textareaRef?.current?.setMarkdown("");
                  if (commentThread.isPending) {
                    deleteThread(commentThread.id);
                  }
                }}
                size="compact-sm"
                variant="secondary"
              >
                Cancel
              </Button>
              <Group>
                <Button
                  onClick={() => setIsSuggesting(true)}
                  size="compact-sm"
                  variant="secondary"
                  disabled={isEmpty}
                >
                  Suggest changes
                </Button>
                <Button
                  onClick={() => saveComment()}
                  size="compact-sm"
                  disabled={isEmpty}
                >
                  Save
                </Button>
              </Group>
            </Group>
          </Stack>
        )}
      </Card.Section>
    </Card>
  );
}

export default function CommentThread(props: Props): ReactElement {
  console.log(props);
  return (
    <ErrorBoundary>
      <CommentThreadImpl {...props} />
    </ErrorBoundary>
  );
}
