import { useMutation } from "react-relay";
import { Disposable, graphql } from "relay-runtime";
import { resolveMutationResolveReviewThreadMutation } from "./__generated__/resolveMutationResolveReviewThreadMutation.graphql";


const resolveMutation = graphql`
  mutation resolveMutationResolveReviewThreadMutation(
    $input: ResolveReviewThreadInput!
  ) {
    resolveReviewThread(input: $input) {
      thread {
        isResolved
      }
    }
  }
`;

export default function useResolveMutation(): [
  (threadId: string, onComplete?: () => void) => Disposable,
  boolean
] {
  const [mutate, isMutating] =
    // @ts-expect-error 2769
    useMutation<resolveMutationResolveReviewThreadMutation>(resolveMutation);
  return [
    (threadId, onComplete) =>
      mutate({
        variables: {
          input: {
            threadId,
          },
        },
        onCompleted: onComplete
      }),
    isMutating,
  ];
}
