import { useAtom } from "jotai";
import { useRelayEnvironment } from "react-relay";
import { commitLocalUpdate } from "relay-runtime";
import { pendingThreadCommentsAtom } from "../review/settings";
import StoreUtils from "./storeUtils";


export function useDeletePendingThreadComment(): (
  reviewThreadId: string
) => void {
  const env = useRelayEnvironment();
  const [pendingReviewThreads, setPendingReviewThreads] = useAtom(
    pendingThreadCommentsAtom
  );
  return (reviewThreadId) => {
    commitLocalUpdate(env, (store) => {
      const pendingReviewThreadId = pendingReviewThreads.get(reviewThreadId);
      store.delete(pendingReviewThreadId);
      setPendingReviewThreads((s) => s.delete(reviewThreadId));
    });
  };
}

export default function usePendingReviewThreadCommentUpdater(): (
  comment: string,
  reviewThreadId: string
) => void {
  const env = useRelayEnvironment();
  const [pendingReviewIds, setPendingReviewId] = useAtom(
    pendingThreadCommentsAtom
  );
  return (comment, reviewThreadId) => {
    commitLocalUpdate(env, (store) => {
      const pendingThreadCommentId = pendingReviewIds.get(reviewThreadId);
      const storeUtils = new StoreUtils(store);
      if (pendingThreadCommentId != null) {
        storeUtils.get(pendingThreadCommentId)?.setValues({
          isPending: true,
          body: comment,
        });
        return;
      }
      const reviewThreadRecord = storeUtils.get(reviewThreadId);
      reviewThreadRecord
        ?.createAndAppendNodeToEdge(
          store,
          "comments",
          {
            first: 20,
          },
          "PullRequestReviewCommentConnection",
          "PullRequestReviewCommentEdge",
          "PullRequestReviewComment",
          {
            isPending: true,
            body: comment,
          }
        )
        ?.then(({ nodeId }) => {
          setPendingReviewId((ids) => ids.set(reviewThreadId, nodeId));
        });
    });
  };
}
