import { graphql } from "relay-runtime";
import { PRTitleFragment$data, PRTitleFragment$key } from "./__generated__/PRTitleFragment.graphql";
import { Badge, Button, Group, Popover, ScrollArea, Stack, Text, Title } from "@mantine/core";
import { useFragment } from "react-relay";
import { useEffect, useMemo, useState } from "react";
import { useGithubQueryWithPath } from "../../api";
import { useQueries, UseQueryResult } from "@tanstack/react-query";
import { useNavigate, useParams } from "react-router-dom";
import { getGHStackInfo, getGraphiteStackInfo } from "./stack";
import { IconChevronDown } from "@tabler/icons-react";
import { first } from "../../utils";

const regex = /(?:https:\/\/github\.com\/[^\/\s]+\/[^\/\s]+\/(?:pull|issues)\/|#)(\d+)/g;

const fragment = graphql`
  fragment PRTitleFragment on PullRequest {
    title
    body
    number
    isDraft
    state
    mergeStateStatus
    comments(first: 30) {
      edges {
        node {
          body
        }
      }
    }
  }
`;

type PRResponse = {
  number: string;
  title: string;
  base: {
    sha: string;
    ref: string;
  };
  head: {
    sha: string;
    ref: string;
  };
};

function useGetPRs(owner: string, name: string, numbers: number[]): UseQueryResult<PRResponse, Error>[] {
  const queryFn = useGithubQueryWithPath<PRResponse>();
  const data = useQueries({
    queries: numbers.map((n) => ({
      queryFn: () => queryFn(`repos/${owner}/${name}/pulls/${n}`),
      queryKey: ["GetPR", owner, name, n],
      staleTime: Infinity,
    })),
  });
  return data;
}

function StatusBadge({ state }: { state: PRTitleFragment$data["state"] }) {
  let text = null;
  let color = null;
  if (state === "MERGED") {
    text = "Merged";
    color = "violet";
  } else if (state === "CLOSED") {
    text = "Closed";
    color = "red";
  } else {
    return;
  }
  return (
    <Badge variant="light" color={color} ml="sm" radius="sm">
      {text}
    </Badge>
  );
}

export default function PRTitle({ pr: prRef }: { pr: PRTitleFragment$key }) {
  // @ts-expect-error 2769
  const pr = useFragment(fragment, prRef);
  const relatedPrs =
    getGHStackInfo(pr.body) ??
    first(
      pr.comments.edges
        ?.map((e) => e?.node?.body)
        ?.map((c) => (c == null ? null : getGraphiteStackInfo(c)))
        ?.filter((c) => c != null)
    )?.reverse() ??
    [];
  const params = useParams();
  const paramWildcard = params["*"];
  const [owner, name] = paramWildcard?.split("/") ?? [];
  const data = useGetPRs(owner, name, relatedPrs);
  const prs = data.map((pr) => pr.data).filter((data) => data != null);
  const number = relatedPrs?.findIndex((p) => p === pr.number);
  const prefix = relatedPrs.length === 0 ? null : `[${number + 1}/${relatedPrs.length}]`;
  const navigate = useNavigate();
  const [isOpened, setIsOpened] = useState(false);
  const title = (
    <Title size="md" lineClamp={1}>
      {prefix}
      {pr.title}
      <StatusBadge state={pr.state} />
    </Title>
  );
  // Effect to close the popover when the route changes
  useEffect(() => {
    setIsOpened(false);
  }, [location.pathname, pr.number]); // Depend on pathname or specific PR identifier
  if (relatedPrs.length === 0) {
    return title;
  }
  return (
    <Popover opened={isOpened} onDismiss={() => setIsOpened(false)} onChange={setIsOpened}>
      <Popover.Target>
        <Button variant="subtle" p={0} color="default" onClick={() => setIsOpened((o) => !o)}>
          {prefix && <IconChevronDown />}
          {title}
          <StatusBadge state={pr.state} />
        </Button>
      </Popover.Target>
      <Popover.Dropdown>
        <ScrollArea h={500}>
          <Stack align="flex-start">
            {prs.map((p, idx) => (
              <Button
                key={p.number}
                onClick={() => {
                  setIsOpened((o) => !o);
                  navigate(`/review/${owner}/${name}/${p.number}`);
                }}
                variant="subtle"
              >
                [{idx + 1}/{relatedPrs.length}]{p.title}
              </Button>
            ))}
          </Stack>
        </ScrollArea>
      </Popover.Dropdown>
    </Popover>
  );
}
