import { useAtom, useAtomValue, useSetAtom } from "jotai";
import { useCallback } from "react";
import { graphql, useMutation } from "react-relay";
import { pendingReviewBodyEditorAtom, pendingReviewIdAtom } from "../review/settings";
import {
  PullRequestReviewEvent,
  submitReviewMutationSubmitPullRequestReviewMutation,
} from "./__generated__/submitReviewMutationSubmitPullRequestReviewMutation.graphql";

const submitReviewMutation = graphql`
  mutation submitReviewMutationSubmitPullRequestReviewMutation(
    $input: SubmitPullRequestReviewInput!
  ) {
    submitPullRequestReview(input: $input) {
      pullRequestReview  {
        id 
        createdAt
        pullRequest {
          id
        }
        ...ActivityPanelReviewFragment
      }
    }
  }
`;

export type ReviewComment = {
  body: string;
  path: string;
  line: number;
};

export default function useSubmitReviewMutation(): [
  (
    pullRequestId: string,
    pullRequestReviewId: string,
    state: PullRequestReviewEvent,
    body: string | null,
  ) => void,
  boolean
] {
  const [_, setPendingReviewId] = useAtom(pendingReviewIdAtom);
  const pendingReviewBodyEditor = useAtomValue(pendingReviewBodyEditorAtom);
  const onCompleteInternal = useCallback(() => {
    setPendingReviewId(null);
    pendingReviewBodyEditor?.current?.setMarkdown("");
  }, [setPendingReviewId, pendingReviewBodyEditor]);

  const [submitPR, isSubmittingPR] =
    useMutation<submitReviewMutationSubmitPullRequestReviewMutation>(
      submitReviewMutation
    );
  return [
    (pullRequestId, pullRequestReviewId, state, body) => {
      submitPR({
        variables: {
          input: {
            pullRequestId: pullRequestId,
            pullRequestReviewId: pullRequestReviewId,
            event: state,
            body: body ?? undefined,
          },
        },
        onCompleted: () => {
          onCompleteInternal();
        },
      });
    },
    isSubmittingPR,
  ];
}
