import {
  RecordProxy,
  RecordSourceProxy,
} from "relay-runtime/lib/store/RelayStoreTypes";
import { v4 as uuid } from "uuid";

const EDGES = "edges";
const NODE = "node";

class Chainable<T> {
  obj: T;
  constructor(obj: T) {
    this.obj = obj;
  }
  then(func: (obj: T) => void) {
    func(this.obj);
  }
}
/**
 * The relay store swallows errors by default and has cumbersome syntax.
 * This class will print errors to console and tidy up the syntax.
 */
class RecordProxyWrapper<T extends object> {
  record: RecordProxy<T>;
  constructor(store: RecordProxy<T>) {
    this.record = store;
  }

  setValues(values: object) {
    console.debug(
      `setValues ${this.record.getDataID()} => ${JSON.stringify(values)}`
    );
    for (const [k, v] of Object.entries(values)) {
      this.record.setValue(v, k);
    }
  }

  setLinkedRecord<T extends object>(
    record: RecordProxyWrapper<T>,
    name: string
  ) {
    this.record.setLinkedRecord(record.record, name);
  }

  setLinkedRecords<T extends object>(
    records: RecordProxyWrapper<T>[],
    name: string
  ) {
    this.record.setLinkedRecords(
      records.map((r) => r.record),
      name
    );
  }

  getEdges(args?: object) {
    const records = this.record.getLinkedRecords(EDGES, args);
    if (records == null) {
      console.log(
        `unable to get edges from ${this.record.getDataID()} of type ${this.record.getType()}`
      );
      return null;
    }
    return records.map((r) => new RecordProxyWrapper(r));
  }

  getNode(fieldName: string, args: object): RecordProxyWrapper<object> | null {
    const record = this.record.getLinkedRecord(fieldName, args);
    if (record == null) {
      console.log(
        `unable to get node ${fieldName} from ${this.record.getDataID()} of type ${this.record.getType()}`
      );
      return null;
    }
    return new RecordProxyWrapper(record);
  }

  /**
   * Inserts a new record of the specified type at the end of the connection
   * e.g.
   * widgets(first:10) {# <-- fieldName, args, connectionType
   *   edges { # <-- edgeType
   *     node { # <-- nodeType
   *       id
   *     }
   *   }
   * }
   * @param fieldName The name of the field that is a connection
   * @param args Args for said field
   * @returns
   */
  createAndAppendNodeToEdge<T extends object>(
    store: RecordSourceProxy,
    fieldName: string,
    args: object,
    connectionType: string,
    edgeType: string,
    nodeType: string,
    nodeValues: T
  ) {
    console.debug(`createAndAppendNodeToEdge ${fieldName} ${args}`);
    let field = this.getNode(fieldName, args);
    if (field == null) {
      const rawField = store.create(uuid(), connectionType);
      console.log(
        `could't find field ${fieldName} on record ${this.record.getDataID()}, creating...`
      );
      this.record.setLinkedRecord(rawField, fieldName, args);
      field = new RecordProxyWrapper(rawField);
    }
    let edges = field.getEdges() ?? null;
    if (edges == null) {
      // const rawField = store.create(uuid(), edgeType);
      console.log(
        `could't find field edges on record ${field.record.getDataID()}, creating...`
      );
      field.record.setLinkedRecords([], EDGES);
      edges = [];
    }
    const edgeId = uuid();
    const nodeId = uuid();
    const newEdge = new RecordProxyWrapper(store.create(edgeId, edgeType));
    const newNode = new RecordProxyWrapper(store.create(nodeId, nodeType));
    newNode.setValues({ ...nodeValues, id: nodeId });
    newEdge.setLinkedRecord(newNode, NODE);
    edges.push(newEdge);
    field.setLinkedRecords(edges, EDGES);
    console.log(`created edge ${edgeId}`);
    return new Chainable({
      edgeId,
      nodeId,
      node: newNode,
    });
  }

  appendToEdge(
    store: RecordSourceProxy,
    fieldName: string,
    args: object,
    edgeType: string,
    nodeId: string
  ) {
    console.debug(`appendToEdge ${fieldName}`);
    const field = this.getNode(fieldName, args);
    if (field == null) return;
    const edges = field.getEdges();
    if (edges == null) return;
    const edgeId = uuid();
    const newEdge = new RecordProxyWrapper(store.create(edgeId, edgeType));
    const node = new StoreUtils(store).get(nodeId);
    if (node == null) {
      console.error(`unable to find node with id ${nodeId}`);
      return;
    }
    newEdge.setLinkedRecord(node, NODE);
    edges.push(newEdge);
    field.setLinkedRecords(edges, EDGES);
    return new Chainable({
      edgeId,
      nodeId,
    });
  }
}

export default class StoreUtils {
  store: RecordSourceProxy;
  constructor(store: RecordSourceProxy) {
    this.store = store;
  }

  get<T extends object>(
    nodeId: string | null | undefined
  ): RecordProxyWrapper<T> | null {
    console.debug(`getRecord ${nodeId}`);
    if (nodeId == null) {
      console.error(`attempted to find null nodeId`);
      return null;
    }
    const record = this.store.get<T>(nodeId);
    if (record == null) {
      console.error(`unable to find record ${nodeId}`);
      return null;
    }
    return new RecordProxyWrapper(record);
  }

  delete(nodeId: string | null | undefined): void {
    if (nodeId != null) {
      this.store.delete(nodeId);
    }
  }
}
