import { useAtom, useSetAtom } from "jotai";
import { commitLocalUpdate, useRelayEnvironment } from "react-relay";
import {
  pendingThreadCommentsAtom,
  pendingThreadsAtom,
} from "../review/settings";
import StoreUtils from "./storeUtils";
import dayjs from "dayjs";

export function useDeleteReviewThreadUpdater(): (
  reviewThreadId: string
) => void {
  const env = useRelayEnvironment();
  const setPendingThreads = useSetAtom(pendingThreadsAtom);
  return (reviewThreadId) =>
    commitLocalUpdate(env, (store) => {
      const storeUtils = new StoreUtils(store);
      storeUtils.delete(reviewThreadId);
      setPendingThreads((t) => t.delete(reviewThreadId));
    });
}

export default function useAddReviewThreadUpdater(): (
  path: string,
  line: number,
  pullRequestId: string,
  endLine?: number
) => void {
  const env = useRelayEnvironment();
  const [pendingThreads, setPendingThreads] = useAtom(pendingThreadsAtom);
  const [pendingThreadComments, setPendingThreadComments] = useAtom(
    pendingThreadCommentsAtom
  );
  return (path, line, pullRequestId, endLine) =>
    commitLocalUpdate(env, (store) => {
      const storeUtils = new StoreUtils(store);
      const entry = pendingThreads.findEntry(
        (v, _k) => v.path === path && v.line === line
      );
      if (entry != null) {
        const [pendingThreadId, pendingThread] = entry;
        const thread = storeUtils.get(pendingThreadId);
        thread?.setValues({
          isPending: true,
          createdAt: dayjs().toISOString(),
          viewerCanReply: true,
        });
        // storeUtils.get(pendingThread.commentId)?.setValues(pendingThread);
        return;
      }
      const data = {
        isPending: true,
        createdAt: dayjs().toISOString(),
        startLine: endLine == null ? null : line,
        line: endLine == null ? line : endLine,
        path,
        viewerCanReply: true,
      };
      storeUtils
        .get(pullRequestId)
        ?.createAndAppendNodeToEdge(
          store,
          "reviewThreads",
          { first: 20 },
          "PullRequestReviewThreadConnection",
          "PullRequestReviewThreadEdge",
          "PullRequestReviewThread",
          data
        )
        ?.then(({ nodeId: reviewThreadId, node: reviewThread }) => {
        //   reviewThread
        //     .createAndAppendNodeToEdge(
        //       store,
        //       "comments",
        //       { first: 20 },
        //       "PullRequestReviewCommentConnection",
        //       "PullRequestReviewCommentEdge",
        //       "PullRequestReviewComment",
        //       data
        //     )
            // ?.then(({ nodeId: commentId }) => {
              setPendingThreads((s) => s.set(reviewThreadId, data));
              // setPendingThreadComments((s) => s.set(reviewThreadId, commentId));
            });
        });
    // });
}
