import { css } from "@emotion/react";
import { Code, Divider, Group, MantineColor, Stack, Text } from "@mantine/core";
import { IconGitCommit, IconGitMerge, IconMessage, IconPencilPlus, IconProps } from "@tabler/icons-react";
import { useAtom, useAtomValue, useSetAtom } from "jotai";
import { ElementType, ReactElement, useCallback, useEffect, useRef, useState } from "react";
import { useFragment } from "react-relay";
import { graphql } from "relay-runtime";
import { userLoginSelector } from "../../state/user";
import { Styles, classNames } from "../../styles";
import { filterNulls, isEmpty, last, useIsFocused } from "../../utils";
import TimeDiffer from "../TimeDiffer";
import usePendingCommentUpdater from "../mutations/pendingComment";
import Comment from "./Comment";
import User from "./User";
import { ActivityPanelFragment$key } from "./__generated__/ActivityPanelFragment.graphql";
import { ActivityPanelReviewFragment$key } from "./__generated__/ActivityPanelReviewFragment.graphql";
import useVersionHistory from "./useVersionHistory";
import { MarkdownTextarea } from "../editor/LazyMarkdownTextarea";
import dayjs, { Dayjs } from "dayjs";
import Footer from "./Footer";
import { hasPendingReviewBodyCommentAtom, pendingReviewBodyEditorAtom } from "./settings";
import { MarkdownTextareaRef } from "../editor/MarkdownTextarea";

const reviewFragment = graphql`
  fragment ActivityPanelReviewFragment on PullRequestReview {
    id
    state
    createdAt
    author {
      ...UserFragment
      login
    }
    body
    commit {
      oid
    }
    ...CommentFragment
    comments(last: 30) {
      edges {
        node {
          body
          author {
            login
          }
        }
      }
    }
  }
`;

const prFragment = graphql`
  fragment ActivityPanelFragment on PullRequest {
    id
    viewerCanUpdate
    mergedAt
    mergedBy {
      login
    }
    ...FooterPullRequestFragment
    reviews(first: 30) {
      edges {
        node {
          author {
            login
          }
          __typename
          createdAt
          id
          state
          ...CommentFragment
          ...ActivityPanelReviewFragment
        }
      }
    }
    comments(first: 30) {
      edges {
        node {
          __typename
          createdAt
          ...CommentFragment
          id
        }
      }
    }
    commits(first: 30) {
      edges {
        node {
          commit {
            oid
          }
        }
      }
    }
    timelineCommits: timelineItems(itemTypes: [PULL_REQUEST_COMMIT], first: 30) {
      edges {
        node {
          ... on PullRequestCommit {
            commit {
              id
              committedDate
              message
              author {
                user {
                  login
                }
              }
            }
          }
        }
      }
    }
    ...useVersionHistoryFragment
  }
`;

function ActivityPanelTextItem({
  login,
  text,
  state,
  from,
  color,
  alreadySeen,
  leftIcon: LeftIcon,
}: Readonly<{
  login: ReactElement;
  text?: string;
  state?: string;
  from: Dayjs;
  color?: MantineColor;
  alreadySeen: boolean;
  leftIcon?: ElementType<IconProps>;
}>): ReactElement {
  return (
    <Group
      className={classNames(state, Styles.secondaryText, alreadySeen && "already-seen-timeline-item", "timeline-item")}
      justify="space-between"
    >
      <Group gap={0}>
        {LeftIcon && <LeftIcon size={16} stroke={1} />}
        <Text size="sm" c={color}>
          {login}&nbsp;{text}
        </Text>
      </Group>
      <Text size="sm">
        <TimeDiffer from={from} prefix={null} />
      </Text>
    </Group>
  );
}

function ActivityPanelItem({
  review: reviewRef,
  currentCommits,
  commits,
  alreadySeen,
}: Readonly<{
  review: ActivityPanelReviewFragment$key;
  currentCommits: Set<string>;
  commits: Map<string, number>;
  alreadySeen: boolean;
}>): ReactElement {
  // @ts-expect-error 2769
  const review = useFragment(reviewFragment, reviewRef);
  let inner = null;
  if (review.state === "APPROVED" || review.state === "CHANGES_REQUESTED") {
    inner = (
      <ActivityPanelTextItem
        alreadySeen={alreadySeen}
        from={dayjs(review.createdAt)}
        login={<User capitalize login={review.author?.login} reviewState={review.state} size="sm"/>}
        state={Styles.colorSuccess}
        text={review.state === "APPROVED" ? "approved" : "requested changes"}
      />
    );
  } else if (review.state === "COMMENTED" && isEmpty(review.body)) {
    // todo refactor
    const currentVersion = review?.commit?.oid == null || currentCommits.has(review.commit.oid);
    const knownPreviousVersion = commits.get(review?.commit?.oid);
    const suffix =
      knownPreviousVersion != null
        ? ` on v${knownPreviousVersion}`
        : currentVersion
        ? ""
        : ` on a previous version (${review?.commit?.oid})`;
    inner = (
      <ActivityPanelTextItem
        alreadySeen={alreadySeen}
        from={dayjs(review.createdAt)}
        login={<User capitalize login={review.author?.login} size="sm" />}
        text={"commented" + suffix}
        leftIcon={IconMessage}
      />
    );
  } else if (review.state === "COMMENTED") {
    inner = <Comment className="timeline-item" comment={review} header="none" variant="small" />;
  }
  return (
    <div
      className={classNames(
        review.state == null && Styles.colorWarning,
        alreadySeen && "already-seen-timeline-item",
        "timeline-item"
      )}
    >
      {inner}
    </div>
  );
}

export default function ActivityPanel({
  pr: prRef,
}: Readonly<{
  pr: ActivityPanelFragment$key;
}>): ReactElement {
  // @ts-expect-error 2769
  const pr = useFragment(prFragment, prRef);
  const [draftComment, setDraftComment] = useAtom(pendingReviewBodyEditorAtom);
  const setHasComment = useSetAtom(hasPendingReviewBodyCommentAtom);
  const currentCommitOids = new Set(filterNulls(pr.commits.edges?.map((e) => e?.node)?.map((n) => n?.commit.oid)));
  const login = useAtomValue(userLoginSelector);
  const { versions: versionHistory } = useVersionHistory(pr);
  const commitShaToVersion = new Map<string, number>();
  // A commit could be in multiple versions so find the first one
  for (const version of versionHistory.slice().reverse()) {
    for (const sha of version.commitOids) {
      commitShaToVersion.set(sha, version.number);
    }
  }
  const commentUpdater = usePendingCommentUpdater();
  const upsertComment = useCallback(
    (comment: string) => {
      // setDraftComment(comment);
      commentUpdater(comment, pr.id);
    },
    [commentUpdater, pr.id, setDraftComment]
  );
  const converterRef = useRef<MarkdownTextareaRef>(null);
  const [forcedWidth, setForcedWidth] = useState<number | undefined>(undefined);
  const ref = useRef<HTMLDivElement>(null);
  const onResizerDrag = useCallback((ev: MouseEvent) => {
    if (ev.preventDefault) {
      ev.preventDefault();
    }
    setForcedWidth(window.innerWidth - ev.x);
  }, []);
  const onMouseUp = useCallback(
    (_ev: Event) => {
      window.removeEventListener("mousemove", onResizerDrag);
      window.removeEventListener("mouseup", onMouseUp);
    },
    [onResizerDrag]
  );
  const onResizerClick = useCallback(
    (ev: MouseEvent) => {
      if (ev.preventDefault) {
        ev.preventDefault();
      }
      window.addEventListener("mousemove", onResizerDrag);
      window.addEventListener("mouseup", onMouseUp);
    },
    [onMouseUp, onResizerDrag]
  );
  useEffect(() => {
    if (ref.current == null) {
      return;
    }
    ref.current.scrollTop = 0;
  }, [ref, pr]);

  const comments = filterNulls(pr.comments?.edges?.map((e) => e?.node)).map((n) => ({
    createdAt: dayjs(n.createdAt),
    node: n,
    type: "comment",
  }));
  const reviews = filterNulls(pr.reviews?.edges?.map((e) => e?.node)).map((n) => ({
    createdAt: dayjs(n.createdAt),
    node: n,
    type: "review",
  }));
  // TODO this createdAt is wrong, it should be the actual force push date not commit
  const versions = filterNulls(versionHistory)
    .filter((e) => e.createdAt != null)
    .map((e) => ({
      createdAt: e.createdAt!,
      node: e,
      type: "versionHistory",
    }));

  const commits = filterNulls(pr.timelineCommits.edges?.map((e) => e?.node)).map((n) => ({
    createdAt: dayjs(n.commit?.committedDate),
    node: n.commit,
    type: "commit",
  }));
  const mergeEvent =
    pr.mergedAt == null
      ? null
      : {
          createdAt: dayjs(pr.mergedAt),
          node: pr.mergedBy,
          type: "merge",
        };
  const allItems = [...comments, ...reviews, ...versions, ...commits, mergeEvent];
  const lastReview = last(filterNulls(pr.reviews?.edges?.map((e) => e?.node)).filter((n) => n.author?.login === login));
  const items = filterNulls(allItems)
    .sort((a, b) => b.createdAt.diff(a.createdAt))
    .map((n) => {
      if (n.type === "review" && n.node?.state != null) {
        const node = n.node;
        return (
          <ActivityPanelItem
            alreadySeen={n?.createdAt < lastReview?.createdAt}
            commits={commitShaToVersion}
            currentCommits={currentCommitOids}
            key={node.id}
            review={node}
          />
        );
      } else if (n.type === "comment") {
        const node = n.node;
        return (
          <Comment
            alreadySeen={n?.createdAt < lastReview?.createdAt}
            className="timeline-item"
            comment={node}
            header="none"
            key={node.id}
            variant={node.id === lastReview?.id ? "normal" : "small"}
          />
        );
      } else if (n.type == "versionHistory") {
        return (
          <Divider
            key={n.node.id}
            label={
              <>
                <IconPencilPlus stroke={1} size={16} />
                <Text ml="xs">
                  <User login={n.node?.author} color="dark.3" /> updated to v{n.node.number}
                </Text>
              </>
            }
            size="lg"
            c="dark"
            my="sm"
          />
        );
      } else if (n.type === "commit") {
        return (
          <Stack key={n.node?.id} gap={0}>
            <Group gap={0} align="center" justify="space-between">
              <Group gap={0}>
                <IconGitCommit stroke={1} css={css({ float: "left" })} size={16} />
                <User login={n.node?.author?.user?.login} size="sm" />
                <Text span lineClamp={3} size="sm">
                  &nbsp;added commit&nbsp;
                </Text>
              </Group>
              <Text>
                <TimeDiffer from={n.createdAt} prefix="" />
              </Text>
            </Group>
            <Code style={{ "--text-line-clamp": 3 }}>{n.node?.message}</Code>
          </Stack>
        );
      } else if (n.type === "merge") {
        return (
          <ActivityPanelTextItem
            leftIcon={IconGitMerge}
            color="violet"
            text={`${n.node.login} merged`}
            key={n.node?.login}
            from={n?.createdAt}
          />
        );
      }
    });

  return (
    <>
      <div className="side-panel-resizer" onMouseDown={(ev) => onResizerClick(ev)} />

      <Stack
        className="side-panel"
        css={css({
          overflowX: "hidden",
          width: forcedWidth,
        })}
        gap={0}
      >
        <Footer pr={pr} />
        <Stack ref={ref} css={css({ overflowY: "auto", overflowX: "hidden", flexGrow: 1 })} gap="xs" mt="xs">
          {items}
        </Stack>
        <Stack css={css({ flex: "0 0 auto" })}>
          <MarkdownTextarea
            instanceId="activityPanel"
            isDisabled={pr.viewerCanUpdate !== true}
            onChange={(isEmpty: boolean) => {
              setHasComment(!isEmpty);
              setDraftComment(converterRef);
            }}
            converterRef={converterRef}
          />
        </Stack>
      </Stack>
    </>
  );
}
