import { css } from "@emotion/react";
import { Group, Skeleton } from "@mantine/core";
import { useAtomValue, useSetAtom } from "jotai";
import { ReactElement, ReactNode, Suspense } from "react";
import { graphql, useLazyLoadQuery } from "react-relay";
import { useParams } from "react-router-dom";
import { sidepanelLayoutAtom } from "../../state/settings";
import { userLoginAtom } from "../../state/user";
import ActivityPanel from "./ActivityPanel";
import CommitsView from "./CommitsView";
import SummaryPanel from "./SummaryPanel";
import { PRReviewCommitsQuery } from "./__generated__/PRReviewCommitsQuery.graphql";
import { useGetScopes } from "../../api";
import { Scopes } from "../../constants";

export const PADDING_PX = 16;

const commitsQuery = graphql`
  query PRReviewCommitsQuery($owner: String!, $name: String!, $id: Int!, $includeTeam: Boolean!) {
    viewer {
      login
    }
    repository(owner: $owner, name: $name) {
      ...CommitsViewRepositoryFragment
      defaultBranchRef {
        name
      }
      pullRequest(number: $id) {
        ...ActivityPanelFragment
        ...FooterPullRequestFragment
        ...CommitsViewPullRequestFragment
        ...SummaryPanelFragment
      }
    }
  }
`;

function LoadingState() {
  return (
    <Layout
      summaryPanel={<Skeleton h="30vh" mb="md" />}
      commitsView={<Skeleton h="50vh" style={{ flexGrow: 1 }} />}
      sidePanel={<Skeleton h="80vh" />}
    />
  );
}

export default function PRReview(): ReactElement {
  return (
    <Suspense fallback={<LoadingState />}>
      <PRReviewImpl />
    </Suspense>
  );
}

function Layout({
  summaryPanel,
  commitsView,
  sidePanel,
}: {
  summaryPanel: ReactNode;
  commitsView: ReactNode;
  sidePanel: ReactNode;
}) {
  return (
    <Group
      css={css({
        height: "100vh",
        overflowY: "hidden",
        flexWrap: "nowrap",
      })}
      gap="sm"
      p="sm"
    >
      <div
        id="commits-container"
        style={{
          flexBasis: "60vw",
          overflowY: "auto",
          height: "100%",
        }}
      >
        {summaryPanel}
        {commitsView}
      </div>
      {/* <Stack style={{ width: 25 }} gap={0}>
        <ScrollBar />
      </Stack> */}
      <div
        style={{
          flexBasis: "30vw",
          flexGrow: 1,
          height: "100%",
          overflowX: "hidden",
        }}
      >
        {sidePanel}
      </div>
    </Group>
  );
}

function PRReviewImpl(): ReactElement | null {
  const params = useParams();
  const paramWildcard = params["*"];
  const [owner, name, id, _commit, _base] = paramWildcard?.split("/") ?? [];
  const setUserLogin = useSetAtom(userLoginAtom);
  const sidepanelLayout = useAtomValue(sidepanelLayoutAtom);
  const scopes = useGetScopes();
  const data = useLazyLoadQuery<PRReviewCommitsQuery>(
    commitsQuery,
    {
      owner: owner,
      name: name,
      id: parseInt(id),
      includeTeam: scopes.has(Scopes.READ_ORG),
    },
    {
      fetchPolicy: "store-and-network",
      // this causes the store to not work when combined with pending comments?
      // networkCacheConfig: {
      //   poll: 1000 * 60 * 5,
      // },
    }
  );
  setUserLogin(data.viewer.login);
  const pullRequest = data.repository?.pullRequest;
  const repo = data.repository;
  const defaultBranchName = data.repository?.defaultBranchRef?.name;
  if (repo == null || paramWildcard == null || pullRequest == null || defaultBranchName == null) {
    return null;
  }

  return (
    <Layout
      summaryPanel={<SummaryPanel pr={pullRequest} />}
      commitsView={
        <CommitsView defaultBranchName={defaultBranchName} name={name} owner={owner} pr={pullRequest} repo={repo} />
      }
      sidePanel={sidepanelLayout === "timeline" && <ActivityPanel pr={pullRequest} />}
    />
  );
}
