import { Map as ImmutableMap } from "immutable";
import { atom } from "jotai";
import { ReactElement, Ref, RefObject } from "react";
import { Version, ViewerLatestReview } from "./useVersionHistory";
import { EditorState } from "lexical";
import { MarkdownTextareaRef } from "../editor/MarkdownTextarea";


export const commentThreadsAtom = atom<Map<string, [ReactElement, number][]>>(
  new Map()
);

export const pendingReviewIdAtom = atom<string | null>(null);

