export const pendingReviewBodyEditorAtom = atom<RefObject<MarkdownTextareaRef> | null>(null);
export const hasPendingReviewBodyCommentAtom = atom<boolean>(false);

/**
 * For every pending thread comment, this is a mapping from the ReviewThread ID
 * to the relay node ID of the comment itself
 */
export const pendingThreadCommentsAtom = atom<ImmutableMap<string, string>>(
  ImmutableMap<string, string>()
);

export type PendingThread = {
  line: number;
  path: string;
  startLine?: number | null;
};

/**
 * NodeIDs of every pending ReviewThread
 */
export const pendingThreadsAtom = atom<ImmutableMap<string, PendingThread>>(
  ImmutableMap<string, PendingThread>()
);

export const activeVersionAtom = atom<Version | null>(null);

export const baseActiveVersionAtom = atom<ViewerLatestReview>(null);
