import { useMantineColorScheme } from "@mantine/core";
import React, { MouseEvent, useCallback, useEffect, useMemo, useState } from "react";
import { useInView } from "react-intersection-observer";
import * as shiki from "shiki";
import { FontStyle } from "@shikijs/vscode-textmate";
import { Change, diffWordsWithSpace } from "diff";
import classes from "./FileDiffer.module.css";
import * as filename2shiki from "filename2shiki";
import { classNames } from "../../styles";
import useAddReviewThreadUpdater from "../mutations/addReviewThreadUpdater";
import CommentThread from "./CommentThread";
import { graphql, useFragment } from "react-relay";
import { FileDifferFragment$data } from "./__generated__/FileDifferFragment.graphql";
import { FileDiffer2Fragment$data, FileDiffer2Fragment$key } from "./__generated__/FileDiffer2Fragment.graphql";
import { filterNulls } from "../../utils";
import { useImmer } from "use-immer";

const worker = new Worker(new URL("./highlightWorker.ts", import.meta.url), {
  type: "module",
});

type DiffLine = {
  oldNumber: number | null;
  newNumber: number | null;
  type: "add" | "del" | "context";
  content: string;
};

type PatchBlock = {
  lineStart: number;
  header?: string;
  lines: DiffLine[];
};

const shikiTokenToStyle = (token: shiki.ThemedToken, themeFgColor?: string): React.CSSProperties => {
  const style: React.CSSProperties = { color: token.color || themeFgColor }; // Use theme FG if token.color is undefined
  if (token.fontStyle) {
    if (token.fontStyle & FontStyle.Italic) style.fontStyle = "italic";
    if (token.fontStyle & FontStyle.Bold) style.fontWeight = "bold";
    if (token.fontStyle & FontStyle.Underline) style.textDecoration = "underline";
  }
  return style;
};

const getDiffClassForPart = (diffPart: Change): string => {
  if (diffPart.added) return classes.intralineAdded;
  if (diffPart.removed) return classes.intralineRemoved;
  return "";
};

function renderLineContent(
  shikiTokensForLine: shiki.ThemedToken[] | undefined,
  charDiffs: Change[],
  lineType: "add" | "del",
  themeFgColor?: string // Pass the theme's default foreground color
): React.ReactNode[] {
  const resultNodes: React.ReactNode[] = [];
  let currentTokenIndex = 0;
  let offsetInToken = 0;
  let charKeyCounter = 0;

  // Handle cases where there are no Shiki tokens for the line at all
  if (!shikiTokensForLine || shikiTokensForLine.length === 0) {
    for (const part of charDiffs) {
      if ((lineType === "del" && part.added) || (lineType === "add" && part.removed)) {
        continue;
      }
      if (part.value) {
        resultNodes.push(
          <span
            key={`no-shiki-${charKeyCounter++}`}
            className={getDiffClassForPart(part)}
            style={themeFgColor ? { color: themeFgColor } : {}} // Apply default fg color
          >
            {part.value}
          </span>
        );
      }
    }
    return resultNodes;
  }

  for (const diffPart of charDiffs) {
    if ((lineType === "del" && diffPart.added) || (lineType === "add" && diffPart.removed)) {
      continue;
    }

    let remainingTextInDiffPart = diffPart.value;
    while (remainingTextInDiffPart.length > 0) {
      if (currentTokenIndex >= shikiTokensForLine.length) {
        // No more Shiki tokens, but still text in this diff part
        if (remainingTextInDiffPart) {
          resultNodes.push(
            <span
              key={`trailing-${charKeyCounter++}`}
              className={getDiffClassForPart(diffPart)}
              style={themeFgColor ? { color: themeFgColor } : {}} // Apply default fg color
            >
              {remainingTextInDiffPart}
            </span>
          );
        }
        break; // Move to the next diff part
      }

      const currentToken = shikiTokensForLine[currentTokenIndex];
      const tokenText = currentToken.content || "";
      const availableTextInToken = tokenText.substring(offsetInToken);

      if (availableTextInToken.length === 0) {
        // Current token is exhausted, move to the next token
        currentTokenIndex++;
        offsetInToken = 0;
        continue; // Re-evaluate with the new token or exit if no more tokens
      }

      const consumeLength = Math.min(availableTextInToken.length, remainingTextInDiffPart.length);
      const textSegment = availableTextInToken.substring(0, consumeLength);

      if (textSegment) {
        resultNodes.push(
          <span
            key={`${charKeyCounter++}-${currentTokenIndex}-${offsetInToken}`}
            style={shikiTokenToStyle(currentToken, themeFgColor)}
            className={getDiffClassForPart(diffPart)}
          >
            {textSegment}
          </span>
        );
      }

      offsetInToken += consumeLength;
      remainingTextInDiffPart = remainingTextInDiffPart.substring(consumeLength);
    }
  }
  return resultNodes;
}

function splitPatches(patch: string): Map<number, string> {
  const lines = patch.split("\n");
  const patches = new Map<number, string>();
  let current: string[] = [];
  let currentNumber = -1;

  for (const [idx, line] of lines.entries()) {
    if (line.startsWith("@@")) {
      if (current.length > 0 && currentNumber !== -1) patches.set(currentNumber, current.join("\n"));
      current = [line];
      currentNumber = idx;
    } else {
      current.push(line);
    }
  }

  if (current.length > 0 && currentNumber !== -1) patches.set(currentNumber, current.join("\n"));
  return patches;
}

function parseGitPatch(patchHunk: string, lineStartInFullPatch: number): PatchBlock {
  const lines = patchHunk.split("\n");
  const result: DiffLine[] = [];
  let blockHeader: string | undefined = undefined;
  let oldLineStartNum = 0;
  let newLineStartNum = 0;

  if (lines.length > 0 && lines[0].startsWith("@@")) {
    const match = /@@ -(\d+)(?:,(\d+))? \+(\d+)(?:,(\d+))? @@(.*)/.exec(lines[0]);
    if (match) {
      oldLineStartNum = parseInt(match[1], 10);
      newLineStartNum = parseInt(match[3], 10);
      blockHeader = match[5] ? match[5].trim() : undefined;
    }
  }

  let currentOldLine = oldLineStartNum;
  let currentNewLine = newLineStartNum;

  for (let i = 1; i < lines.length; i++) {
    const line = lines[i];
    if (line.startsWith("+")) {
      result.push({
        oldNumber: null,
        newNumber: currentNewLine++,
        type: "add",
        content: line.slice(1),
      });
    } else if (line.startsWith("-")) {
      result.push({
        oldNumber: currentOldLine++,
        newNumber: null,
        type: "del",
        content: line.slice(1),
      });
    } else if (line.startsWith(" ")) {
      result.push({
        oldNumber: currentOldLine++,
        newNumber: currentNewLine++,
        type: "context",
        content: line.slice(1),
      });
    } else if (line.startsWith("\\") || line.trim() === "") {
      // Handle "\ No newline..." or empty lines if they affect Shiki's line count.
      // If Shiki also receives these empty strings and tokenizes them (e.g. as []),
      // then including them as DiffLine with empty content might be necessary for alignment.
      // For now, let's assume Shiki handles content lines passed and `parseGitPatch` should match that.
      // If an empty line in the patch (e.g., a blank line that was added/deleted) is significant:
      if (
        line.trim() === "" &&
        (lines[i - 1]?.startsWith("+") || lines[i - 1]?.startsWith("-") || lines[i - 1]?.startsWith(" "))
      ) {
        // This is complex. If the line before was part of the diff, this might be an empty line in the diff.
        // For now, strict parsing of +, -, ' ' lines.
      }
    }
  }
  return {
    lines: result,
    lineStart: lineStartInFullPatch,
    header: blockHeader,
  };
}

const prFragment = graphql`
  fragment FileDiffer2Fragment on PullRequest {
    id
    ...CommentThreadPullRequestFragment
    reviewThreads(first: 20) {
      edges {
        node {
          id
          path
          line
          ...CommentThreadPullRequestReviewFragment
        }
      }
    }
  }
`;

type ReviewThread = NonNullable<
  NonNullable<NonNullable<FileDiffer2Fragment$data["reviewThreads"]["edges"]>[number]>["node"]
>;

export default function FileDiffer2({
  patch,
  fileName,
  pr: prRef,
}: {
  patch: string;
  fileName: string;
  pr: FileDiffer2Fragment$key;
}) {
  console.log("render");
  const { colorScheme } = useMantineColorScheme();
  const [tokensMap, setTokensMap] = useImmer<Record<string, shiki.TokensResult>>({});
  const { ref } = useInView({ rootMargin: "1000px" });
  const inView = true;
  const pr = useFragment(prFragment, prRef);
  const blocks = useMemo(() => {
    if (!inView || !patch) return [];
    const patchStrings = splitPatches(patch);
    return Array.from(patchStrings.entries()).map(([lineNum, patchStr]) => parseGitPatch(patchStr, lineNum));
  }, [inView, patch]);

  const knownIds = useMemo(
    () => new Set(inView ? blocks.map((b) => `${fileName}_${b.lineStart}`) : []),
    [blocks, fileName, inView]
  );
  const [isSelecting, setIsSelecting] = useState(false);
  const [highlightedRows, setHighlightedRows] = useImmer<(number | null)[]>([null, null]);
  const [expandedComemnts, setExpandedComments] = useImmer(new Set());
  const addReviewThread = useAddReviewThreadUpdater();
  // In case the mouse up happes outside the linenumber column
  const onWindowMouseUp = useCallback(() => {
    setIsSelecting(false);
    setHighlightedRows([null, null]);
  }, [setHighlightedRows]);
  const onMouseDown = useCallback(
    (ev: MouseEvent) => {
      const row = parseInt(ev.target?.parentElement?.getAttribute("data-line"));
      setIsSelecting(true);
      setHighlightedRows((s) => [row, s[1]]);
      window.addEventListener("mouseup", onWindowMouseUp);
    },
    [setHighlightedRows, onWindowMouseUp]
  );
  const reviewThreads = filterNulls(
    pr?.reviewThreads?.edges
      ?.map((e) => e?.node)
      ?.flat()
      ?.filter((c) => c != null && c?.path == fileName)
  ).reduce((acc, curr) => {
    const line = curr.line;
    const rt = curr;
    if (acc.has(line)) {
      acc.get(line)?.push(rt);
    } else {
      acc.set(line, [rt]);
    }
    return acc;
  }, new Map<number | null | undefined, ReviewThread[]>());
  const onMouseUp = useCallback(
    (ev: MouseEvent) => {
      const endRow = parseInt(ev.target?.parentElement?.getAttribute("data-line"));
      setIsSelecting(false);
      window.removeEventListener("mouseup", onWindowMouseUp);
      const [startRow, _endRow] = highlightedRows;
      const threadsAtLine = new Set(reviewThreads.get(endRow)?.map((rt) => rt.startLine ?? endRow) ?? []);
      if (startRow == null) {
        return;
      }
      if (threadsAtLine.has(endRow ?? null)) {
        return;
      }
      if (startRow == endRow) {
        addReviewThread(fileName, endRow, pr.id);
      } else {
        addReviewThread(fileName, startRow, pr.id, endRow);
      }
      setHighlightedRows([null, null]);
      if (ev.preventDefault) {
        ev.preventDefault();
      }
    },
    [onWindowMouseUp, reviewThreads, highlightedRows, setHighlightedRows, addReviewThread, fileName, pr.id]
  );
  const onMouseEnter = useCallback(
    (ev: MouseEvent) => {
      const row = parseInt(ev.target?.parentElement?.getAttribute("data-line"));
      if (isSelecting) {
        setHighlightedRows((s) => [s[0], row]);
      }
    },
    [setHighlightedRows, isSelecting]
  );

  useEffect(() => {
    if (!inView || blocks.length === 0) {
      return;
    }

    blocks.forEach(({ lines, lineStart }) => {
      const codeLines = lines.map((l) => l.content);
      if (codeLines.length > 0) {
        const langForShiki = filename2shiki.findOne(fileName) || "plaintext";
        worker.postMessage({
          type: "highlight",
          id: `${fileName}_${lineStart}`,
          code: codeLines.join("\n"),
          lang: langForShiki,
          dark: colorScheme === "dark",
        });
      }
    });
  }, [fileName, colorScheme, inView, blocks]);

  useEffect(() => {
    const handler = (e: MessageEvent) => {
      if (e.data.type === "result" && knownIds.has(e.data.id)) {
        setTokensMap((prev) => ({ ...prev, [e.data.id]: e.data.tokens }));
      }
    };
    worker.addEventListener("message", handler);
    return () => worker.removeEventListener("message", handler);
  }, [knownIds, setTokensMap]);
  const toggleThread = useCallback(
    (ev: MouseEvent) => {
      const line = ev.target?.parentElement?.getAttribute("data-line");
      if (line == null) {
        return;
      }
      const lintInt = parseInt(line);
      setExpandedComments((ec) => {
        ec.has(lintInt) ? ec.delete(lintInt) : ec.add(lintInt);
      });
    },
    [setExpandedComments]
  );

  if (!inView && blocks.length === 0 && patch) {
    return <div ref={ref} style={{ minHeight: "200px" }} />;
  }

  return (
    <div ref={ref}>
      {blocks.map((block) => {
        const blockTokensResult = tokensMap[`${fileName}_${block.lineStart}`];
        const themeFgColor = blockTokensResult?.fg; // Get theme's default foreground

        return (
          <div key={`${fileName}_${block.lineStart}`}>
            {block.header && <div className={classes.blockHeader}>{block.header}</div>}
            <table className={classes.fileTable}>
              <tbody>
                {block.lines.map((line, lineIndex) => {
                  const currentLineTokens = blockTokensResult?.tokens?.[lineIndex];

                  let charDiffs: Change[] | null = null;
                  let isPartOfPair = false;

                  if (
                    line.type === "del" &&
                    lineIndex + 1 < block.lines.length &&
                    block.lines[lineIndex + 1].type === "add"
                  ) {
                    charDiffs = diffWordsWithSpace(line.content, block.lines[lineIndex + 1].content);
                    isPartOfPair = true;
                  } else if (line.type === "add" && lineIndex > 0 && block.lines[lineIndex - 1].type === "del") {
                    charDiffs = diffWordsWithSpace(block.lines[lineIndex - 1].content, line.content);
                    isPartOfPair = true;
                  }

                  const attemptIntraline = (line.type === "add" || line.type === "del") && isPartOfPair && charDiffs;

                  return (
                    <>
                      <tr
                        key={`${block.lineStart}_${line.oldNumber}_${line.newNumber}`}
                        className={classNames(
                          line.type === "add" && classes.addedLine,
                          line.type === "del" && classes.removedLine
                        )}
                        data-line={line.newNumber}
                      >
                        <td
                          className={classNames(
                            classes.lineNumber,
                            reviewThreads.has(line.newNumber) && classes.lineNumberComment
                          )}
                          onClick={toggleThread}
                        >
                          {line.oldNumber ?? ""}
                        </td>
                        <td
                          className={classNames(
                            classes.lineNumber,
                            classes.lineNumberRight,
                            reviewThreads.has(line.newNumber) && classes.lineNumberComment
                          )}
                          data-line={line.newNumber}
                          onMouseDown={onMouseDown}
                          onMouseEnter={onMouseEnter}
                          onMouseUp={onMouseUp}
                          onClick={toggleThread}
                        >
                          {line.newNumber ?? ""}
                        </td>
                        <td className={classNames(classes.codeCell, line.type === "context" && classes.contextLine)}>
                          {attemptIntraline ? (
                            renderLineContent(currentLineTokens, charDiffs!, line.type as "add" | "del", themeFgColor)
                          ) : currentLineTokens && currentLineTokens.length > 0 ? (
                            currentLineTokens.map((token, j) => (
                              <span key={j} style={shikiTokenToStyle(token, themeFgColor)}>
                                {token.content}
                              </span>
                            ))
                          ) : (
                            <span
                              style={{
                                whiteSpace: "pre-wrap",
                                color: themeFgColor || "inherit",
                              }}
                            >
                              {/* Apply theme FG or inherit for raw content */}
                              {line.content}
                            </span>
                          )}
                        </td>
                      </tr>
                      {expandedComemnts.has(line.newNumber) &&
                        reviewThreads.get(line.newNumber)?.map((rt) => (
                          <tr className="comment-container" key={rt.id}>
                            <td colSpan={3}>
                              <CommentThread pr={pr} reviewThread={rt} />
                            </td>
                          </tr>
                        ))}
                    </>
                  );
                })}
              </tbody>
            </table>
          </div>
        );
      })}
    </div>
  );
}
