import { useAtomValue } from "jotai";
import { type ReactElement } from "react";
import { graphql, useFragment, useLazyLoadQuery } from "react-relay";
import { useGetCompareCommits } from "../../api";
import { reviewSplitByCommitSelector } from "../../state/settings";
import { filterNulls, last, only } from "../../utils";
import ErrorBoundary from "../ErrorBoundary";
import CommitView from "./CommitView";
import { CommitsViewPullRequestFragment$key } from "./__generated__/CommitsViewPullRequestFragment.graphql";
import { CommitsViewRepositoryFragment$key } from "./__generated__/CommitsViewRepositoryFragment.graphql";
import { CommitsViewSelectedCommitsQuery } from "./__generated__/CommitsViewSelectedCommitsQuery.graphql";
import { activeVersionAtom, baseActiveVersionAtom } from "./settings";
import { Alert } from "@mantine/core";


const reviewFragment = graphql`
  fragment CommitsViewPullRequestFragment on PullRequest {
    __typename
    baseRef {
      name
    }
    commits(first: 30) {
      totalCount
      edges {
        node {
          commit {
            id
            ...CommitViewFragment
            parents(first: 2) {
              edges {
                node {
                  oid
                  message
                }
              }
            }
          }
        }
      }
    }
    ...CommitViewPullRequestFragment
  }
`;

const selectedCommitsQuery = graphql`
  query CommitsViewSelectedCommitsQuery($ids: [ID!]!) {
    nodes(ids: $ids) {
      ... on Commit {
        ...CommitViewFragment
        id
        parents(first: 2) {
          edges {
            node {
              oid
            }
          }
        }
      }
    }
  }
`;

const repositoryFragment = graphql`
  fragment CommitsViewRepositoryFragment on Repository {
    ...CommitViewRepositoryFragment
  }
`;

type Props = Readonly<{
  owner: string;
  name: string;
  defaultBranchName: string;
  pr: CommitsViewPullRequestFragment$key;
  repo: CommitsViewRepositoryFragment$key;
}>;

function CommitsViewImpl({
  owner,
  name,
  pr: prRef,
  repo: repoRef,
}: Props): ReactElement | null {
  const pr = useFragment(reviewFragment, prRef);
  const repo = useFragment(repositoryFragment, repoRef);
  const baseActiveVersion = useAtomValue(baseActiveVersionAtom);
  const activeVersion = useAtomValue(activeVersionAtom);
  const reviewSplitByCommit = useAtomValue(reviewSplitByCommitSelector);
  const commitsFromBaseVersion = useGetCompareCommits(
    owner,
    name,
    baseActiveVersion?.oid,
    activeVersion?.headOid,
  );
  const commitsFromVersionData =
    useLazyLoadQuery<CommitsViewSelectedCommitsQuery>(
      selectedCommitsQuery,
      {
        ids:
          commitsFromBaseVersion?.commits.map((e) => e.node_id) ??
          activeVersion?.nodeIds ??
          [],
      },
      {
        fetchPolicy:
          activeVersion == null && baseActiveVersion == null
            ? "store-only"
            : "store-or-network",
        fetchKey: `${activeVersion?.headOid}...${baseActiveVersion?.oid}`
      }
    ) ?? null;

  const prCommits = filterNulls(pr.commits.edges?.map((e) => e?.node?.commit));

  const commits =
    activeVersion == null ? prCommits : commitsFromVersionData.nodes ?? [];

  const numMissingCommits = (pr.commits?.totalCount ?? 0) - commits.length;
  const lastCommit = last(commits);
  if (lastCommit == null) {
    return null;
  }
  if (!reviewSplitByCommit) {
    return (
      <CommitView
        baseRef={
          (baseActiveVersion?.oid ??
            activeVersion?.baseRef ??
            pr.baseRef?.name)!
        }
        commit={lastCommit}
        name={name}
        owner={owner}
        pr={pr}
        repo={repo}
      />
    );
  }

  const commitComponents = commits.map((commit) => {
    if (commit == null || repo == null) {
      return null;
    }
    const baseRef = only(commit.parents?.edges?.map((e) => e?.node));
    return (
      <CommitView
        baseRef={baseActiveVersion?.oid ?? baseRef?.oid}
        commit={commit}
        key={commit.id}
        name={name}
        owner={owner}
        pr={pr}
        repo={repo}
      />
    );
  });
  return (
    <>
      {numMissingCommits > 0 && (
        <Alert variant="warning">
          <span>
            {numMissingCommits} more commits and pagination is not implemented!
          </span>
        </Alert>
      )}
      {commitComponents}
    </>
  );
}

export default function CommitsView(props: Props) {
  return (
    <ErrorBoundary>
      <CommitsViewImpl {...props} />
    </ErrorBoundary>
  );
}
