import { Badge, Button, Card, Group, Modal, Stack, Text } from "@mantine/core";
import { ReactElement, ReactNode, Suspense, useRef, useState } from "react";
import { graphql, useFragment } from "react-relay";
import LoadingSpinner from "../LoadingSpinner";
import TimeDiffer from "../TimeDiffer";
import User from "./User";
import { CommentFragment$key } from "./__generated__/CommentFragment.graphql";
import { MarkdownRenderer } from "../markdown/LazyMarkdownRenderer";
import dayjs from "dayjs";


const fragment = graphql`
  fragment CommentFragment on Comment {
    body
    author {
      login
    }
    createdAt
    isPending
    ... on PullRequestReviewComment {
      reviewState: state
    }
  }
`;

type Props = Readonly<{
  comment: CommentFragment$key;
  className?: string;
  variant?: "normal" | "small";
  header?: "none" | "default";
  style?: React.CSSProperties;
  right?: ReactNode | undefined;
  bottomBorder?: boolean;
  border?: boolean;
}>;

const COMMENT_HEIGHT_LIMIT = 500;

function CommentImpl({
  comment: commentRef,
  header = "default",
  right = undefined,
  border = true,
}: Props): ReactElement {
  const data = useFragment(fragment, commentRef);
  const [showFull, setShowFull] = useState(false);
  const isPending = data.isPending || data.reviewState === "PENDING";
  const contentRef = useRef<HTMLDivElement>(null);
  const body = (
    <Stack gap="xs">
      <Group wrap="nowrap" justify="space-between">
        <Group gap={0}>
          {data.author != null && (
            <User capitalize login={data.author.login} size="sm" />
          )}
          <Text span size="sm">
            &nbsp;commented&nbsp;
          </Text>
          <TimeDiffer from={dayjs(data.createdAt)} prefix={null} />
          {isPending && (
            <Badge variant="light" color="yellow.8" ml="sm" radius="sm">
              Pending
            </Badge>
          )}
        </Group>
        <Group>
          {(contentRef.current?.clientHeight ?? 0) > COMMENT_HEIGHT_LIMIT && (
            <Button size="compact-xs" onClick={() => setShowFull(true)}>
              ...
            </Button>
          )}
          {right}
        </Group>
      </Group>
      <MarkdownRenderer ref={contentRef}>{data.body}</MarkdownRenderer>
    </Stack>
  );
  return (
    <Card
      mah={COMMENT_HEIGHT_LIMIT}
      withBorder={border}
      p="xs"
      radius={0}
      style={{flexShrink: 0}}
    >
      {header === "default" && (
        <Group justify="space-between">
          {data.author != null && <User login={data.author.login} />}
          <TimeDiffer from={dayjs(data.createdAt)} prefix={null} />
          {isPending && <Badge c="yellow">Pending</Badge>}
        </Group>
      )}
      {body}
      {showFull && (
        <Modal size="lg" opened onClose={() => setShowFull(false)}>
          <MarkdownRenderer>{data.body}</MarkdownRenderer>
        </Modal>
      )}
    </Card>
  );
}

export default function Comment(props: Props): ReactElement {
  return (
    <Suspense fallback={<LoadingSpinner />}>
      <CommentImpl {...props} />
    </Suspense>
  );
}
