import { Card, Group, Text } from "@mantine/core";
import { ReactElement, Suspense } from "react";
import { graphql, useFragment } from "react-relay";
import { ComparePRResultFile } from "../../api";
import { Styles, classNames } from "../../styles";
import { only } from "../../utils";
import LoadingSpinner from "../LoadingSpinner";
import { useToggle } from "../layout/Toggle";
import ActionMenu from "./ActionMenu";
import CopyableFilePath from "./CopyableFilePath";
import FileDiffer from "./FileDiffer";
import { PADDING_PX } from "./PRReview";
import { CommitFileCommitFragment$key } from "./__generated__/CommitFileCommitFragment.graphql";
import { CommitFileFragment$key } from "./__generated__/CommitFileFragment.graphql";
import { CommitFileRepositoryFragment$key } from "./__generated__/CommitFileRepositoryFragment.graphql";
import FileDiffer2 from "./FileDiffer2";

const fragment = graphql`
  fragment CommitFileFragment on PullRequest {
    ...FileDiffer2Fragment
  }
`;

const repoFragment = graphql`
  fragment CommitFileRepositoryFragment on Repository {
    owner {
      login
    }
    name
    defaultBranchRef {
      name
    }
  }
`;
const commitFragment = graphql`
  fragment CommitFileCommitFragment on Commit {
    oid
    parents(first: 2) {
      edges {
        node {
          oid
        }
      }
    }
  }
`;

type Props = Readonly<{
  pr: CommitFileFragment$key;
  repo: CommitFileRepositoryFragment$key;
  commit: CommitFileCommitFragment$key;
  compareData: ComparePRResultFile;
}>;

export default function CommitFile(props: Props): ReactElement {
  return (
    <Suspense fallback={<LoadingSpinner />}>
      <CommitFileImpl {...props} />
    </Suspense>
  );
}

function CommitFileImpl({
  pr: prRef,
  repo: repoRef,
  commit: commitRef,
  compareData: c,
}: Props): ReactElement {
  const pr = useFragment(fragment, prRef);
  const repo = useFragment(repoFragment, repoRef);
  const commit = useFragment(commitFragment, commitRef);
  const parent = only(commit.parents.edges?.map((e) => e?.node));
  const [isHidden, toggle, setIsHidden] = useToggle();
  return (
    <Card className="file-header" key={c.filename} mb="md" withBorder>
      <Card.Section
        inheritPadding
        // Prevent text selection on double click
        onMouseDown={(e) => {
          if (e.detail > 1) {
            e.preventDefault();
          }
        }}
        // onClick={(e) => {
        //   setIsHidden((h) => !h);
        // }}
        // To offset the overall padding
        style={{ top: -PADDING_PX }}
        withBorder
        py={6}
      >
        <Group align="center" justify="start" wrap="nowrap">
          <CopyableFilePath path={c.filename} />
          <Text style={{ whiteSpace: "pre" }}>
            {" "}
            <Text span c={"green.8"}>
              +{c.additions}
            </Text>{" "}
            <Text span c={"red.8"}>
              -{c.deletions}
            </Text>
          </Text>
          <Group align="center" style={{ marginLeft: "auto" }} gap="sm">
            <ActionMenu
              commitOid={commit.oid}
              defaultBranchName={repo.defaultBranchRef?.name}
              filename={c.filename}
              name={repo.name}
              owner={repo.owner.login}
              parentCommitOid={parent?.oid}
            />
            {toggle}
          </Group>
        </Group>
      </Card.Section>
      {!isHidden && (
        <Card.Section style={{ padding: 0 }}>
          {c.patch == null ? (
            <Text pl="sm">No diff for this file</Text>
          ) : (
            <FileDiffer2
              fileName={c.filename}
              patch={c.patch}
              pr={pr}
            />
          )}
        </Card.Section>
      )}
    </Card>
  );
}
