import { ActionIcon, Badge, Card, Group, Stack, Text, Title } from "@mantine/core";
import { IconAlertCircle, IconCheck, IconExclamationCircle, IconExternalLink } from "@tabler/icons-react";
import { ReactElement, Suspense } from "react";
import { useRefetchableFragment } from "react-relay";
import { graphql } from "relay-runtime";
import { filterNulls, isEmpty, only } from "../../utils";
import { useToggle } from "../layout/Toggle";
import LoadingSpinner from "../LoadingSpinner";
import TimeDiffer from "../TimeDiffer";
import {
  PullRequestReviewState,
  SummaryPanelFragment$data,
  SummaryPanelFragment$key,
} from "./__generated__/SummaryPanelFragment.graphql";
import User from "./User";
import dayjs from "dayjs";
import PRTitle from "./PRTitle";
import { MarkdownRenderer } from "../markdown/LazyMarkdownRenderer";
import { getBodyWithoutGHStack } from "./stack";
import { useAtom, useAtomValue } from "jotai";
import { userLoginAtom } from "../../state/user";

const fragment = graphql`
  fragment SummaryPanelFragment on PullRequest @refetchable(queryName: "SummaryPanelFragment2") {
    mergeable
    url
    title
    closed
    body
    createdAt
    viewerCanMergeAsAdmin
    ...PRTitleFragment
    baseRef {
      branchProtectionRule {
        requiresCodeOwnerReviews
        requiredApprovingReviewCount
      }
    }
    author {
      login
    }
    reviews(first: 30) {
      edges {
        node {
          state
          createdAt
          author {
            login
          }
        }
      }
    }
    reviewRequests(first: 30) {
      edges {
        node {
          asCodeOwner
          requestedReviewer {
            ... on User {
              login
            }
            ... on Team @include(if: $includeTeam) {
              name
            }
          }
        }
      }
    }
    lastCommit: commits(last: 1) {
      edges {
        node {
          commit {
            statusCheckRollup {
              state
              contexts(first: 50) {
                totalCount
                checkRunCountsByState {
                  count
                  state
                }
                statusContextCountsByState {
                  count
                  state
                }
                edges {
                  node {
                    ... on CheckRun {
                      __typename
                      id
                      detailsUrl
                      name
                      status
                      conclusion
                    }
                    ... on StatusContext {
                      __typename
                      id
                      state
                      description
                      context
                      avatarUrl
                      targetUrl
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
`;

type Props = Readonly<{
  pr: SummaryPanelFragment$key;
}>;

export default function SummaryPanel(props: Props): ReactElement {
  return (
    <Suspense fallback={<LoadingSpinner />}>
      <SummaryPanelImpl {...props} />
    </Suspense>
  );
}

function getCheckOrStatusRuns(
  obj:
    | NonNullable<SummaryPanelFragment$data["lastCommit"]["edges"][]>[number]["node"]
    | NonNullable<SummaryPanelFragment$data["lastCommit"]["edges"]>[number]["node"]
) {
  return {};
}

function sortState(state: PullRequestReviewState | null | undefined) {
  switch (state) {
    case "CHANGES_REQUESTED":
      return 50;
    case "APPROVED":
      return 40;
    case "COMMENTED":
      return 30;
    default:
      return 10;
  }
}

function SummaryPanelImpl({ pr: prRef }: Props): ReactElement {
  // @ts-expect-error 2769
  const [pullRequest, refetch] = useRefetchableFragment(fragment, prRef);
  const body = getBodyWithoutGHStack(pullRequest.body).trim();
  const lastCommit = only(filterNulls(pullRequest.lastCommit.edges?.map((e) => e?.node)))?.commit;
  const checkRuns = filterNulls(lastCommit?.statusCheckRollup?.contexts?.edges?.map((e) => e?.node));
  const currentUser = useAtomValue(userLoginAtom);
  const numRunsPassed =
    (only(lastCommit?.statusCheckRollup?.contexts?.checkRunCountsByState?.filter((cr) => cr.state === "SUCCESS"))
      ?.count ?? 0) +
    (only(lastCommit?.statusCheckRollup?.contexts.statusContextCountsByState?.filter((sc) => sc.state === "SUCCESS"))
      ?.count ?? 0);
  const allCIPassing = lastCommit?.statusCheckRollup?.contexts?.totalCount === numRunsPassed;
  const [isHidden, toggle] = useToggle(allCIPassing);
  const reviewers = new Map<
    string,
    {
      participated: boolean;
      required: boolean;
      state?: PullRequestReviewState;
      date?: dayjs.Dayjs;
      isTeam?: boolean;
    }
  >();
  pullRequest.reviewRequests?.edges?.reduce((acc, curr) => {
    const login = curr?.node?.requestedReviewer?.login;
    if (login != null) {
      acc.set(login, {
        participated: false,
        required:
          acc.get(login)?.required ||
          ((curr?.node?.asCodeOwner &&
            !pullRequest.viewerCanMergeAsAdmin &&
            pullRequest.baseRef?.branchProtectionRule?.requiresCodeOwnerReviews) ??
            false),
      });
    }
    const name = curr?.node?.requestedReviewer?.name;
    if (name != null) {
      acc.set(name, {
        participated: false,
        required:
          acc.get(name)?.required ||
          ((curr?.node?.asCodeOwner &&
            !pullRequest.viewerCanMergeAsAdmin &&
            pullRequest.baseRef?.branchProtectionRule?.requiresCodeOwnerReviews) ??
            false),
        isTeam: true,
      });
    }
    return acc;
  }, reviewers);
  pullRequest.reviews?.edges?.reduce((acc, curr) => {
    const login = curr?.node?.author?.login;
    if (login == null) {
      return acc;
    }
    const existing = acc.get(login);
    acc.set(login, {
      participated: true,
      required: existing?.required ?? false,
    });
    if (
      existing == null ||
      existing.date == null ||
      (existing.date < dayjs(curr?.node?.createdAt) && curr?.node?.state !== "COMMENTED")
    ) {
      acc.set(login, {
        participated: true,
        required: existing?.required ?? false,
        state: curr?.node?.state ?? existing?.state,
        date: dayjs(curr?.node?.createdAt),
      });
    }
    return acc;
  }, reviewers);
  const allReviews = Array.from(reviewers.entries());
  allReviews
    .filter(([user, _]) => user != currentUser)
    .sort(([_a, { state: stateA }], [_b, { state: stateB }]) => {
      return sortState(stateA) - sortState(stateB);
    });
  const checkRunsComponent = (
    <Stack style={{ maxHeight: 200, overflow: "scroll", marginTop: 16 }}>
      {filterNulls(checkRuns)
        .sort((a, b) => {
          return -1;
        })
        .map((check) => {
          if (check.__typename !== "CheckRun" && check.__typename !== "StatusContext") {
            return null;
          }
          const body =
            check.__typename === "CheckRun"
              ? `${check.name}`
              : check.__typename === "StatusContext"
              ? `${check.context}: ${check.description}`
              : "";
          const checkIsSuccessful =
            (check.__typename === "CheckRun" && check.conclusion === "SUCCESS") ||
            (check.__typename === "StatusContext" && check.state === "SUCCESS");
          const checkInProgress =
            (check.__typename === "CheckRun" && check.status === "IN_PROGRESS") ||
            (check.__typename === "StatusContext" && check.state === "PENDING");
          const avatarUrl = check.__typename === "StatusContext" ? check.avatarUrl : null;
          const detailsUrl =
            check.__typename === "StatusContext"
              ? check.targetUrl
              : check.__typename === "CheckRun"
              ? check.detailsUrl
              : null;
          return (
            <Stack key={check.id}>
              <Group align="center">
                {checkIsSuccessful ? (
                  <IconCheck color="var(--mantine-color-green-8)" />
                ) : checkInProgress ? (
                  <IconAlertCircle color="var(--mantine-color-yellow-8)" />
                ) : (
                  <IconExclamationCircle color="var(--mantine-color-red-8)" />
                )}
                {avatarUrl != null && (
                  <img
                    src={avatarUrl}
                    style={{
                      width: 16,
                      marginRight: 4,
                      objectFit: "contain",
                    }}
                  />
                )}
                {body}
                <div className="float-right">
                  <a href={detailsUrl} rel="noreferrer" target="_blank">
                    Details
                  </a>
                </div>
              </Group>
            </Stack>
          );
        })}
    </Stack>
  );
  const ciStatusComponent = (
    <Card.Section withBorder inheritPadding p="xs">
      <Group justify="space-between">
        <Group>
          <Badge
            radius="sm"
            color={
              pullRequest.mergeable === "MERGEABLE"
                ? "green.8"
                : pullRequest.mergeable === "CONFLICTING"
                ? "red"
                : "gray"
            }
            style={{ marginRight: 8 }}
          >
            {!pullRequest.closed && pullRequest.mergeable === "MERGEABLE"
              ? "Mergeable"
              : pullRequest.mergeable === "CONFLICTING"
              ? "Merge Conflicts"
              : "Unknown merge state"}
          </Badge>
          {(lastCommit?.statusCheckRollup?.contexts?.totalCount ?? 0) > 0 && (
            <Badge
              radius="sm"
              color={
                lastCommit?.statusCheckRollup?.state === "SUCCESS"
                  ? "green.8"
                  : lastCommit?.statusCheckRollup?.state === "FAILURE"
                  ? "red"
                  : "yellow.7"
              }
              style={{ marginRight: 8 }}
            >
              CI ({numRunsPassed}/{lastCommit?.statusCheckRollup?.contexts?.totalCount})
            </Badge>
          )}
        </Group>
        {allCIPassing && toggle}
      </Group>
      {!isHidden && checkRunsComponent}
    </Card.Section>
  );
  return (
    <Card mb="md" withBorder px="md">
      <Card.Section inheritPadding p="xs" withBorder>
        <Group justify="space-between" align="center" wrap="nowrap">
          <PRTitle pr={pullRequest} />
          <Group justify="flex-end" align="center">
            <ActionIcon component="a" variant="default" href={pullRequest.url} target="_blank">
              <IconExternalLink stroke={1} />
            </ActionIcon>
          </Group>
        </Group>
        <Text size="sm" lineClamp={1} span>
          <TimeDiffer from={dayjs(pullRequest.createdAt)} prefix="Created " /> by{" "}
          <User login={pullRequest.author?.login} size="sm" />
        </Text>
      </Card.Section>
      <Card.Section withBorder inheritPadding py="xs">
        <MarkdownRenderer>{body == null || isEmpty(body) ? "*No description provided*" : body}</MarkdownRenderer>
      </Card.Section>
      <Card.Section withBorder inheritPadding p="xs">
        <Group>
          <Title size="sm">Reviewers</Title>
          <Group gap={0} justify="space-between">
            <Group>
              {Array.from(allReviews).map(([login, { participated, required, state }]) => (
                <User
                  size="sm"
                  key={login}
                  login={login}
                  reviewState={state ?? (participated ? "COMMENTED" : "requested")}
                  required={required}
                />
              ))}
            </Group>
            <Group gap={0}>
              {pullRequest.baseRef?.branchProtectionRule?.requiresCodeOwnerReviews &&
                pullRequest.viewerCanMergeAsAdmin && <Text>Codeowners are required</Text>}
              {pullRequest.baseRef?.branchProtectionRule?.requiredApprovingReviewCount != null && (
                <Text>
                  Requires {pullRequest.baseRef?.branchProtectionRule?.requiredApprovingReviewCount} approvals
                </Text>
              )}
            </Group>
          </Group>
        </Group>
      </Card.Section>
      {ciStatusComponent}
    </Card>
  );
}
