const regex =
  /Stack from \[ghstack\].+?\(oldest at bottom\):\r?\n(?:\*[^\n]+\r?\n?)+/gm;
const hashPattern = /#[0-9]{1,}/g;

export function getGHStackInfo(body: string): number[] {
  const blocks = body.match(regex);

  return (
    blocks
      ?.flatMap((block) => block.match(hashPattern) ?? [])
      ?.map((s) => parseInt(s.slice(1))) ?? []
  );
}

export function getBodyWithoutGHStack(body: string): string {
  const matches = body.match(regex);
  if (matches && matches.length > 0) {
    return body.replace(matches[0], "");
  }
  return body;
}

const graphiteRegex =
  /(?:\*[\n]+\n)+This stack of pull requests is manged by \[Graphite\]/gm;
export function getGraphiteStackInfo(body: string): number[] {
  const blocks = body.match(graphiteRegex);

  return (
    blocks
      ?.flatMap((block) => block.match(hashPattern) ?? [])
      ?.map((s) => parseInt(s.slice(1))) ?? []
  );
}
