import { useAtomValue } from "jotai";
import { ReactElement, useRef } from "react";
import { graphql, useFragment } from "react-relay";
import { useGetCompareCommits } from "../../api";
import { reviewSplitByCommitSelector } from "../../state/settings";
import { Styles } from "../../styles";
import { filterNulls, only } from "../../utils";
import { useToggle } from "../layout/Toggle";
import CommitFile from "./CommitFile";
import { CommitViewFragment$key } from "./__generated__/CommitViewFragment.graphql";
import { CommitViewPullRequestFragment$key } from "./__generated__/CommitViewPullRequestFragment.graphql";
import { CommitViewRepositoryFragment$key } from "./__generated__/CommitViewRepositoryFragment.graphql";
import { Alert, Group } from "@mantine/core";


const fragment = graphql`
  fragment CommitViewFragment on Commit {
    ...CommitFileCommitFragment
    oid
    messageHeadline
    additions
    deletions
    message
    parents(first: 2) {
      edges {
        node {
          oid
          message
        }
      }
    }
  }
`;

const repositoryFragment = graphql`
  fragment CommitViewRepositoryFragment on Repository {
    ...CommitFileRepositoryFragment
  }
`;

const prFragment = graphql`
  fragment CommitViewPullRequestFragment on PullRequest {
    ...CommitFileFragment
  }
`;

export default function CommitView({
  owner,
  name,
  commit: commitRef,
  pr: prRef,
  repo: repoRef,
  baseRef,
}: Readonly<{
  name: string;
  commit: CommitViewFragment$key;
  repo: CommitViewRepositoryFragment$key;
  pr: CommitViewPullRequestFragment$key;
  baseRef: string;
  owner: string;
}>): ReactElement | null {
  const commit = useFragment(fragment, commitRef);
  const repo = useFragment(repositoryFragment, repoRef);
  const pr = useFragment(prFragment, prRef);
  const reviewSplitByCommit = useAtomValue(reviewSplitByCommitSelector);
  const parents = filterNulls(
    commit?.parents?.edges?.map((e) => e?.node) ?? []
  );
  const parent = only(parents);
  const compareData = useGetCompareCommits(
    owner,
    name,
    baseRef,
    commit.oid,
    !reviewSplitByCommit || parent != null
  );
  const ref = useRef<HTMLDivElement>(null);
  const [commitIsHidden, commitToggle] = useToggle();
  if (reviewSplitByCommit && parents?.length > 1) {
    // TODO probably better way to do this...
    return (
      <Alert className="commit-header" ref={ref} variant="info">
        {commit.message}
      </Alert>
    );
  }
  return (
    <>
      {reviewSplitByCommit ? 
        <Alert className="commit-header" ref={ref} variant="info">
          <Group>
            <div>
              Commit: {commit.messageHeadline} -{" "}
              <span className={Styles.colorSuccess}>+{commit.additions}</span>{" "}
              <span className={Styles.colorDanger}>-{commit.deletions}</span>
            </div>
            {commitToggle}
          </Group>
        </Alert> : null}
      {!commitIsHidden &&
        compareData?.files?.map((c) => (
          <CommitFile
            commit={commit}
            compareData={c}
            key={c.filename}
            pr={pr}
            repo={repo}
          />
        ))}
    </>
  );
}
