import { css } from "@emotion/react";
import { Breadcrumbs, CopyButton, Text, Tooltip } from "@mantine/core";
import { ReactElement } from "react";


export default function CopyableFilePath({
  path,
}: Readonly<{
  path: string;
}>): ReactElement {
  const pieces = path.split("/");
  return (
    <Breadcrumbs separatorMargin='xxxs' style={{lineClamp: 1, overflowX:'hidden'}}>
      {pieces.map((piece, idx) => {
        const path = pieces.slice(idx).join("/");
        return (
          <CopyButton key={path} value={path}>
            {({ copied, copy }) => (
              <Tooltip
                label={copied ? "Copied" : `Copy ${path}`}
                withArrow
                position="bottom"
              >
                <Text
                  component="a"
                  css={css({ whiteSpace: "nowrap" })}
                  onClick={copy}
                >
                  {piece}
                </Text>
              </Tooltip>
            )}
          </CopyButton>
        );
      })}
    </Breadcrumbs>
  );
}
