import { Badge, Button, Card, Group, Popover, ScrollArea, Table, Text } from "@mantine/core";
import { ReactElement, useState } from "react";
import { useFragment } from "react-relay";
import { graphql } from "relay-runtime";
import ErrorBoundary from "../ErrorBoundary";
import { VersionSelectorFragment$key } from "./__generated__/VersionSelectorFragment.graphql";
import useVersionHistory from "./useVersionHistory";


const fragment = graphql`
  fragment VersionSelectorFragment on PullRequest {
    ...useVersionHistoryFragment
    repository {
      defaultBranchRef {
        name
      }
    }
  }
`;

type Props = Readonly<{
  fragment: VersionSelectorFragment$key;
}>;

export default function VersionSelector(props: Props): ReactElement {
  return (
    <ErrorBoundary>
      <VersionSelectorImpl {...props} />
    </ErrorBoundary>
  );
}

function VersionSelectorImpl({ fragment: fragmentRef }: Props): ReactElement {
  // @ts-expect-error 2769
  const pr = useFragment(fragment, fragmentRef);
  const { versions: versionHistory } = useVersionHistory(pr);

  // State to track the selected version, defaulting to the latest version
  const [selectedVersion, setSelectedVersion] = useState(
    versionHistory[versionHistory.length - 1]
  );

  return (
    <Popover position="bottom" withArrow shadow="md">
      <Popover.Target>
        <Button variant="light" size="compact-sm">
          v{versionHistory[versionHistory.length - 1]?.number}
        </Button>
      </Popover.Target>
      <Popover.Dropdown>
        <Group style={{ display: "flex", gap: "1rem" }}>
          {/* Left Panel: List of Versions */}
          <Card shadow="sm" padding="lg" style={{ flex: 1 }}>
            <Text size="lg" mb="md" style={{ fontWeight: 700 }}>
              Versions
            </Text>
            <ScrollArea style={{ height: 400 }}>
              {versionHistory.map((version) => (
                <Card
                  key={version.number}
                  shadow="xs"
                  padding="sm"
                  style={{
                    marginBottom: "0.5rem",
                    cursor: "pointer",
                    backgroundColor:
                      selectedVersion?.number === version.number
                        ? "#f0f0f0"
                        : "white",
                  }}
                  onClick={() => setSelectedVersion(version)}
                >
                  <Group style={{ justifyContent: "space-between", alignItems: "center" }}>
                    <Text>
                      <strong>v{version.number}</strong> - {version.baseRef}
                    </Text>
                    <Badge color="blue">{version.commitOids.length} commits</Badge>
                  </Group>
                </Card>
              ))}
            </ScrollArea>
          </Card>

          {/* Right Panel: Commits for Selected Version */}
          <Card shadow="sm" padding="lg" style={{ flex: 2 }}>
            <Text size="lg" mb="md" style={{ fontWeight: 700 }}>
              Commits for v{selectedVersion?.number}
            </Text>
            {selectedVersion ? (
              <ScrollArea style={{ height: 400 }}>
                <Table>
                  <thead>
                    <tr>
                      <th>Commit Hash</th>
                      <th>Author</th>
                      <th>Date</th>
                    </tr>
                  </thead>
                  <tbody>
                    {selectedVersion.commitOids.map((commitOid) => (
                      <tr key={commitOid}>
                        <td>{commitOid}</td>
                        <td>{selectedVersion.author}</td>
                        <td>
                          {selectedVersion.createdAt
                            ? selectedVersion.createdAt.format("D MMM YYYY HH:mm")
                            : "Unknown"}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </Table>
              </ScrollArea>
            ) : (
              <Text>No version selected.</Text>
            )}
          </Card>
        </Group>
      </Popover.Dropdown>
    </Popover>
  );
}
